.search{
  &__inner{
    position: relative;
  }
  &__top{
    margin-top: 1.125em;
    @include grid100gap(.5em);
  }
  &__items{
    margin-top: 1.125em;
    @include grid100gap(1.125em);
  }
}

.search-card{
  padding: 1.25em 1.75em;
  border-radius: .3125em;
  background-color: $lightGrayBg;
  @include grid100gap(.75em);
  letter-spacing: 0.025em;
  &__items{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    color: #6f6f6f;
    display: flex;
    margin-right: .5em;
    &-img,
    &-text{
      display: inline-flex;
    }
    &-img{
      align-self: flex-start;
      margin-right: .3125em;
      width: .875em;
      min-height: 1.25em;
      flex-shrink: 0;
      &:not(:first-child){
        margin-left: .3125em;
      }
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

.pagination{
  margin-top: 1.25em;
  display: flex;
  align-items: center;
  &__link{
    @include center;
    width: 2.75em;
    height: 2.75em;
    border-radius: .3125em;
    background-color: $lightGrayBg;
    letter-spacing: 0.025em;
    margin-right: .5em;
    span{
      font-size: 1em;
      line-height: 120%;
    }
    &:hover{
      color: #fff;
      background-color: $primary;
    }
    &._active{
      pointer-events: none;
      color: #fff;
      background-color: #ca5422;
    }
  }
  &__next{
    margin-left: 1em;
    img{
      height: .5625em;
    }
  }
}


@media screen and (max-width: 576px){
  .search-card{
    padding: 1em;
    &__item{
      margin-top: .5em;
    }
  }
}
