.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;

    &__bg {
        background: rgba(#181818, 0.94);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &--active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        border-radius: .3125em;
        width: 22.5em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
    }

    &__scroll {
        overflow-y: auto;
        max-height: calc(100 * var(--vh));
        padding: 1.25em;
    }

    &__close {
        position: absolute;
        top: .625em;
        right: .625em;
        width: .75em;
        img,
        svg {
            display: block;
            width: 100%;
            height: auto;
        }
    }
    &__info{
        text-align: center;
        .text27{
            margin-top: -.125em;
        }
    }
    .success-mess{
        background-color: transparent;
        padding: 0;
        box-shadow: none;
    }
}

.modal-form{
    margin-top: .625em;
    @include grid100gap(.5em);
    textarea{
        height: 5em;
    }
    .m-check{
        letter-spacing: unset;
        label{
            span{
                font-size: .75em;
                line-height: 117%;
            }
        }
    }
    &__btn{
        margin-top: .375em;
        height: 3.125em;
        border-radius: .3125em;
    }
}

.modal-product{
    margin-top: .5em;
    display: flex;
    padding: .375em .5em;
    border-radius: .625em;
    background-color: $lightGrayBg;
    &__img{
        flex-shrink: 0;
        width: 4.125em;
        margin-right: .875em;
        img{
            width: 100%;
            display: block;
        }
    }
    &__text{
        @include truncate(2);
        letter-spacing: 0.025em;
        font-size: .8125em;
        line-height: 138%;
    }
    &__price{
        margin-top: .375em;
    }
}

.confirm-mess{
    @include grid100gap(.875em);
    text-align: center;
    padding-top: .625em;
    &__nav{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .375em));
        gap: .75em;
    }
    &__btn{
        height: 3.125em;
        border-radius: .3125em;
    }
}

.help-modal{
    .modal__dialog{
        background-color: $textBlack;
        border-radius: .3125em;
        width: 15em;
    }
    .modal__scroll{
        padding: 0;
        border-radius: .3125em;
    }
    .modal__close{
        background-color: #555555;
        width: 1.5em;
        height: 1.5em;
        top: -.375em;
        right: -.375em;
        @include center;
        border-radius: 50%;
        img{
            width: .5625em;
        }
    }
    &__top{
        letter-spacing: 0.025em;
        color: #ffffff;
        text-align: center;
        padding: .25em;
        min-height: 3.5em;
        @include center;
        background-color: #555555;
        border-radius: .3125em .3125em 0 0;
    }
    &__link{
        @include center;
        text-align: center;
        padding: .5em;
        letter-spacing: 0.025em;
        color: #ffffff;
        &:not(:first-child){
            border-top: 1px solid #5c5c5c;
        }
        span{
            font-size: .875em;
            line-height: 129%;
        }
    }
    &__btn{
        width: 100%;
        height: 3.375em;
        border-radius: 0 0 .3125em .3125em;
    }
}
