.services-1c{
  margin-top: 3em;
  &--mt0{
    margin-top: 0;
  }
  &--2m{
    margin-top: 2em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: .875em 1.25em;
    margin-top: 1.25em;
  }
  &__link{
    width: 100%;
    height: 5em;
    border-radius: .5em;
    text-align: center;
    span{
      font-size: 1.0625em;
      line-height: 142%;
      letter-spacing: 0.025em;
    }
    &._active{
      background-color: $primary;
      color: #fff;
      pointer-events: none;
    }
  }
}

.services-page,
.work-detail{
  &__inner{
    position: relative;
  }
  &__rects{
    pointer-events: none;
    position: absolute;
    z-index: -1;
    bottom: 1.5em;
    left: -3.0625em;
    width: 92.625em;
  }
}

.services-page{
  padding: 0 0 2.375em;
  .direction-consult{
    margin-bottom: 0;
  }
}

.work-detail{
  padding: 2.75em 0;
  background-color: $lightGrayBg;
  &__rects{
    z-index: unset;
  }
}

.work-table{
  position: relative;
  z-index: 2;
  margin-top: 1em;
  table{
    width: 100%;
    border-spacing: 0 .5em;
  }
  tbody{
    tr{
      transition: all .3s;
      &:hover{
        box-shadow: .125em .25em 2.1875em 0 rgba(90,91,91,0.18);
      }
    }
  }
  td,
  th{
    padding-left: 1.25em;
    text-align: left;
  }
  td{
    overflow: hidden;
    background-color: #fff;
    height: 3.25em;
    letter-spacing: 0.025em;
    &:first-child{
      border-radius: .5em 0 0 .5em;
      width: 22em;
    }
    &:last-child{
      border-radius: 0 .5em .5em 0;
      width: 21em;
    }
  }
  th{
    span{
      font-size: 1.125em;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__action{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 6.25em;
    height: 100%;
    border-radius: 0 .5em .5em 0;
  }
  &__title{
    display: none;
  }
  .text{
    padding: .5em 0;
    ol{
      &:first-child{
        margin-top: 0;
      }
    }
  }
  @media screen and (min-width: 993px){
    &._type2{
      td{
        &:last-child{
          width: 16.25em;
        }
      }
      .work-table{
        &__action{
          justify-content: flex-end;
        }
        &__price{
          margin-right: 1.25em;
          text-align: right;
        }
        &__btn{
          width: 7em;
        }
      }
    }
  }
}

.equip2._white-bg{
  background-color: #fff;
}

.direction-consult--1c{
  max-width: 52.5em;
  .text16{
    line-height: 168.75%;
    margin-top: -.75em;
  }
}

.service-detail{
  padding: .25em 0 2.125em;
  &__inner{
    display: grid;
    grid-template-columns: 18.3125em calc(100% - 20.5625em);
    gap: 2.25em;
    position: relative;
  }
  &__info{
    border-radius: .625em;
    background-color: $lightGrayBg;
    padding: 1.75em 2.375em 2.375em;
  }
  &__advantages{
    margin-top: 1em;
  }
  .video{
    margin-top: 1.75em;
  }
  &__rects{
    position: absolute;
    z-index: -1;
    width: 7.625em;
    pointer-events: none;
  }
  &__rects1{
    top: 1.875em;
    left: -4.5em;
  }
  &__rects2{
    top: -3em;
    right: -3.0625em;
    width: 15.25em;
  }
  &__rects3{
    top: 48.125em;
    right: -3.0625em;
  }
  &__rects4{
    left: 17.75em;
    top: 59.8125em;
  }
  &__rects5{
    left: 14.75em;
    top: 94.125em;
  }
}

.service-nav{
  &__link{
    background-color: $lightGrayBg;
    display: flex;
    align-items: center;
    min-height: 3em;
    padding: .5em 1.625em;
    &:nth-child(1){
      border-top-left-radius: .625em;
      border-top-right-radius: .625em;
    }
    &:last-child{
      border-bottom-left-radius: .625em;
      border-bottom-right-radius: .625em;
    }
    &:hover{
      background-color: #fff;
      color: $black;
    }
  }
}

.advantage-card3{
  padding: .75em 1.125em 1.125em;
  background-color: #ffffff;
  border-radius: .3125em;
  &__icon{
    height: 2.875em;
    margin-bottom: .625em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__info{
    @include grid100gap(.3125em);
    letter-spacing: 0.025em;
    .text15{
      hyphens: auto;
    }
  }
}

.video{
  position: relative;
  display: block;
  img{
    width: 100%;
    display: block;
    border-radius: .3125em;
  }
  &__btn{
    @include centerAbsolute;
    width: 5.3125em;
    svg{
      width: 100%;
      height: auto;
      border-radius: 50%;
      box-shadow: .3125em .625em 3.375em 0 rgba(91,92,92,0.79);
    }
    path{
      fill: rgba(255,255,255,0.58);
    }
    &:hover{
      path{
        fill: rgba(255,255,255,0.8);
      }
    }
  }
}

.service-group{
  margin-top: 1.375em;
  letter-spacing: 0.025em;
  &__list{
    margin-top: 1em;
  }
  &__bottom{
    margin-top: 1em;
    display: flex;
    align-items: center;
  }
  &__btn{
    min-width: 9.25em;
    border-radius: .3125em;
    padding: 0 2.25em;
    height: 3.625em;
    margin-right: 1em;
    flex-shrink: 0;
    span{
      letter-spacing: 0.025em;
    }
  }
}

.service-price{
  margin-top: 2.625em;
  .service-group{
    margin-top: .625em;
    padding: 1em 1.25em 1.25em;
    background-color: #ffffff;
    border-radius: .3125em;
    &:not(:last-child){
      margin-bottom: .875em;
    }
  }
}

.support{
  margin-top: 2em;
  padding: 1.5em 1.5em 1.375em 2.375em;
  display: grid;
  grid-template-columns: calc(100% - 15.8125em) 15.3125em;
  gap: .5em;
  background-color: #ca5422;
  background-image: linear-gradient(90.0deg, #ea5626 0%, #fc6d3f 100%);
  border-radius: .625em;
  color: #fff;
  &__img{
    margin-top: -.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  .text16{
    margin-top: .5em;
  }
  .check-list{
    margin-top: .5em;
    li:before{
      background-image: url(../img/check4.svg);
    }
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__btn{
    padding: 0 2.25em;
    height: 3.8125em;
  }
}



@media screen and (max-width: 992px){
  .services-page{
    padding-bottom: 1.5em;
    &__rects{
      display: none;
    }
  }

  .services-1c{
    margin-top: 2em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
    &__link{
      height: 3.5em;
    }
  }

  .work-detail{
    padding: 1.5em 0;
    &__rects{
      display: none;
    }
  }

  .work-table{
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(1.5em);
    }
    tr{
      @include grid100gap(1em);
      background-color: #fff;
      border-radius: .5em;
      padding: 1.25em;
    }
    td{
      padding: 0;
      background-color: transparent;
      width: 100% !important;
      height: unset;
      display: block;
    }
    &__title{
      display: block;
      margin-bottom: .5em;
      color: $textGray;
      font-weight: 700;
      &:after{
        content: ':';
      }
    }
    &__action{
      @include grid100gap(1em);
    }
    &__btn{
      height: 3.5em;
      width: 100%;
      border-radius: .5em;
    }
  }

  .service-detail{
    padding-bottom: 0;
    &__rects{
      display: none;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__sidebar{
      display: none;
    }
    &__info{
      border-radius: 0;
      @include fullScreen;
    }
  }

  .video{
    &__btn{
      width: 3.5em;
    }
  }

  .service-group{
    &__bottom{
      flex-direction: column;
      align-items: unset;
    }
    &__price{
      text-align: center;
      margin-bottom: 1em;
    }
    &__hint{
      margin-bottom: 1em;
    }
    &__btn{
      order: 2;
      max-width: 100%;
      margin: 0;
    }
  }

  .support{
    grid-template-columns: 100%;
    @include fullScreen;
    border-radius: 0;
    margin-top: 0;
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .services-1c{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
