.automatization-consult{
  padding-bottom: 2.1875em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 32.25em) 30.5em;
    gap: 1.25em 1.75em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__info{
    @include grid100gap(.5em);
  }
  &__nav2{
    margin-top: auto;
    padding-top: .5em;
  }
  &__btn{
    height: 3.75em;
    border-radius: .5em;
    img{
      width: 1.625em;
      margin-right: .625em;
      flex-shrink: 0;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.help{
  padding: 2.625em 0;
  background-color: $lightGrayBg;
  &__inner{
    position: relative;
  }
  .working-principe__rects,
  .working-principe__rects2,
  .working-principe__rects3{
    z-index: 1;
  }
  @media screen and (min-width: 993px){
    .working-principe__inner{
      padding-bottom: 2.125em;
      padding-top: 2.125em;
    }
    .working-principe__quote{
      bottom: -1.875em;
    }
  }
  &__grid{
    margin-top: 1.5em;
    position: relative;
    z-index: 2;
  }
}

.help-card{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  border-radius: .3125em;
  min-height: 6.375em;
  padding: 0 2.625em;
  &__icon{
    width: 4em;
    height: 4em;
    flex-shrink: 0;
    @include center;
    margin-right: .625em;
    svg{
      width: 100%;
      height: 100%;
      fill: $primary;
    }
  }
}

.automatization-cases{
  padding: 2.25em 0 2.5em;
  &__inner{
    position: relative;
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__grid{
    margin-top: 1em;
  }
  &__show-more{
    margin: 1.5em auto 0;
    width: calc(100% / 3 - 3.25em / 3);
    height: 4em;
    border-radius: .5em;
    span{
      font-size: 1em;
    }
    img{
      width: 1.625em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__rects,
  &__rects2{
    position: absolute;
    z-index: -1;
    bottom: 2.5em;
  }
  &__rects{
    left: 9.125em;
    width: 6.125em;
  }
  &__rects2{
    right: -3.0625em;
    width: 6.3125em;
  }
}

.automatization-card{
  background-color: $lightGrayBg;
  border-radius: .5em;
  transition: all .3s;
  &:hover{
    box-shadow: .25em .4375em 3.1875em 0 rgba(90,91,91,0.43);
  }
  &._new{
    display: none;
  }
  &__img{
    display: block;
    @include imgCover(53%);
    img{
      border-radius: .5em .5em 0 0;
    }
  }
  &__content{
    padding: 1.25em 1.5em;
  }
  &__desc{
    margin-top: .5em;
  }
}

.surprised{
  padding: 3em 0 2.75em;
  background-color: $lightGrayBg;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 25em) 22.5em;
    gap: 2.5em;
    position: relative;
  }
  &__col{
    position: relative;
    z-index: 2;
  }
  &__text{
    p{
      font-size: 1em;
      line-height: 169%;
      @include lastM(0 0 1.5em);
    }
  }
  &__rects{
    top: 8em;
    right: -3.125em;
    position: absolute;
    width: 6.3125em;
  }
}

.surprised-sidebar{
  padding: 1.75em 1.625em 1.5em;
  text-align: center;
  background-color: #fff;
  border-radius: .625em;
  &__icon{
    display: block;
    margin: 1em auto .625em;
    width: 5.375em;
  }
  &__desc{
    margin-top: .625em;
  }
  &__btn{
    width: 100%;
    height: 4em;
    border-radius: .5em;
    margin-top: 1em;
    span{
      font-size: 1.125em;
    }
  }
}

.we-can{
  padding: 2.25em 0 2.5em;
  &__grid{
    margin-top: 1em;
  }
  .sector-card{
    background-color: $lightGrayBg;
    min-height: 6.375em;
    svg{
      fill: $primary;
    }
    &:hover{
      cursor: default;
      svg{
        fill: #fff;
      }
    }
  }
}

.automatization-banner{
  padding-bottom: .875em;
  color: #fff;
  &__inner{
    position: relative;
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    display: flex;
    align-items: center;
    max-width: 40em;
    width: 100%;
    z-index: 2;
  }
  &__img{
    width: 100%;
    min-height: 20em;
    img{
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      border-radius: .5em;
    }
  }
  &__info{
    @include grid100gap(1em);
    padding: 2em 6.25em;
  }
  &__rects,
  &__rects2{
    width: 6.125em;
    position: absolute;
    z-index: -1;
  }
  &__rects{
    bottom: -.1875em;
    left: -3.0625em;
  }
  &__rects2{
    bottom: 6.75em;
    right: -4.375em;
  }
  &__text{
    margin-top: 1.5em;
  }
}

.automatization-gallery{
  padding: .875em 0 1.75em;
  overflow: hidden;
}

.automatization-slider{
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
      width: calc(100% + 1.75em);
    }
    &-slide{
      width: 50%;
      padding-right: 1.75em;
    }
    &-pagination{
      position: static;
      margin-top: 1.875em;
      &-bullet{
        background-color: $textBlack;
      }
    }
  }
  &__img{
    display: block;
    @include imgCover(68%);
    img{
      border-radius: .5em;
    }
  }
}

.automatization-desc{
  padding: 2.25em 0;
  background-color: $lightGrayBg;
  &__inner{
    column-count: 2;
    column-gap: 1.875em;
    line-height: 167%;
    p{
      line-height: inherit;
    }
  }
}

.target{
  padding: 2.375em 0;
  &__items{
    margin-top: 1.5em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__bottom{
    margin-top: .75em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__by{
    font-style: italic;
    color: #777777;
  }
  &__btn{
    margin-top: 1.25em;
    padding: 0 2.125em;
    height: 4em;
    border-radius: .5em;
    span{
      font-size: 1em;
    }
  }
  &__split{
    display: flex;
    align-items: center;
    margin: 1.75em 0;
    &:before,
    &:after{
      content: '';
      width: 2em;
      height: 1px;
      background-color: #d9d9d9;
      flex-grow: 1;
    }
    &-text{
      color: #898989;
      flex-shrink: 0;
      margin: 0 .5em;
    }
  }
  &__round{
    @include center;
    width: 2.375em;
    height: 2.375em;
    background-color: $primary;
    border-radius: 50%;
    margin: 0 .5em;
    flex-shrink: 0;
    svg{
      width: 1em;
      height: auto;
    }
  }
  &__rects,
  &__rects2{
    position: absolute;
    z-index: -1;
  }
  &__rects{
    top: -2.375em;
    left: -8.625em;
    width: 98.25em;
  }
  &__rects2{
    top: .6875em;
    left: -3.0625em;
    width: 89.6875em;
  }
}

.target-badge{
  position: relative;
  margin-top: 3em;
  min-height: 8em;
  text-align: center;
  background-color: $textBlack;
  color: #fff;
  border-radius: .625em;
  padding: 1.5em;
  @include grid100gap(0);
  justify-content: center;
  align-items: center;
  &:first-child{
    margin-top: 0;
  }
  &:before{
    top: 100%;
    left: 50%;
  }
  &._primary{
    background-color: $primary;
  }
}

.target-card{
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border-radius: .3125em;
  background-color: $lightGrayBg;
  hyphens: auto;
  transition: all .3s;
  &:hover{
    background-color: $primary;
    color: #fff;
    svg{
      fill: #fff;
    }
  }
  &__icon{
    width: 2.0625em;
    height: 2.0625em;
    margin-bottom: .625em;
    svg{
      width: 100%;
      height: 100%;
      fill: $primary;
    }
  }
}

.target-info{
  margin-top: 1.5em;
  text-align: center;
  &__text2{
    margin-top: .25em;
  }
  &__text3{
    margin-top: .75em;
  }
  @media screen and (min-width: 993px){
    &__title{
      br{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .automatization-consult{
    padding-bottom: 1em;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .help{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
  }

  .help-card{
    justify-content: unset;
    padding: 0 1.5em;
    &__title{
      font-size: 1em;
    }
  }

  .automatization-cases{
    padding: 1.5em 0;
    &__rects,
    &__rects2{
      display: none;
    }
    &__show-more{
      width: 100%;
    }
  }

  .surprised{
    padding: 1.5em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__rects{
      display: none;
    }
  }

  .we-can{
    padding: 1.5em 0;
  }

  .automatization-banner{
    color: $textBlack;
    &__img{
      min-height: unset;
      img{
        min-height: 15em;
      }
    }
    &__content{
      position: static;
      margin-top: .75em;
    }
    &__info{
      padding: 0;
    }
    .text40{
      font-size: 1.5em;
    }
    .text18{
      font-size: 1em;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .automatization-gallery{
    padding: 1.5em 0;
  }

  .automatization-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
      &-pagination{
        display: none;
      }
    }
  }

  .automatization-desc{
    &__inner{
      column-count: 1;
    }
  }

  .target{
    padding: 1.5em 0;
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .target-badge{
    margin-top: 1.5em;
    gap: .5em;
  }

  .target-card{
    padding: .75em;
    &__title{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .help{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
