@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.tabs{
  &__current{
    display: none;
  }
}

.cartcalc{
  width: 5.8125em;
  height: 2.5em;
  display: grid;
  grid-template-columns: 1.75em calc(100% - 3.5em) 1.75em;
  border: 1px solid $grayBg;
  border-radius: .5em;
  button{
    height: 100%;
    width: 100%;
    @include center;
    background-color: $grayBg;
    &:hover{
      background-color: $primary;
      color: #fff;
    }
    span{
      font-size: 1.125em;
      line-height: 117%;
    }
  }
  input{
    height: 100%;
    width: 100%;
    border: none;
    font-size: 1.125em;
    line-height: 117%;
    text-align: center;
  }
  .ccalc-minus{
    border-radius: .5em 0 0 .5em;
  }
  .ccalc-plus{
    border-radius: 0 .5em .5em 0;
  }
}

.swiper-nav{
  .swiper-button{
    width: 2.8125em;
    height: 2.4375em;
    background-color: $primary;
    //opacity: 1;
    transform: translateY(-50%);
    margin: 0;
    svg{
      max-width: 1.25em;
      max-height: 1.25em;
      stroke: #fff;
      fill: none;
    }
    &-prev{
      border-radius: 50% 0 0 50%;
      left: unset;
      right: 100%;
    }
    &-next{
      border-radius: 0 50% 50% 0;
      right: unset;
      left: 100%;
    }
  }
}

.swiper-pagination-bullet{
  opacity: 1;
  background-color: #fff;
  width: .4375em;
  height: .4375em;
  border-radius: 50%;
  margin: 0 .5em !important;
  &-active{
    transform: scale(1.57);
  }
}

.grid-column6{
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6 - 5em / 6));
  gap: 1em;
}

.grid-column4{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
  gap: 1.5em;
}

.grid-column3{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 3.25em / 3));
  gap: 1.625em;
}

._rect-before{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    left: 100%;
    top: 50%;
    width: 1.25em;
    height: 1.25em;
    transform: translate(-50%,-50%) rotate(-45deg);
    background-color: inherit;
    border-radius: .125em;
  }
}

._gray-bg{
  background-color: $lightGrayBg;
}

.fancybox{
  &__toolbar{
    padding: 0;
  }
  &__button{
    &--fullscreen,
    &--zoom{
      display: none;
    }
    &--close{
      width: 2.5em;
      height: 2.5em;
      background-color: #fff;
      border-radius: 0 0 0 .625em;
      background-image: url(../img/close.svg);
      @include bgImgSize(.625em);
      svg{
        display: none;
      }
    }
  }
  &__caption{
    //position: absolute;
    //bottom: 0;
    //left: 0;
    padding: 1.5em 2em;
    color: #cecece;
    font-size: .75em;
    line-height: 120%;
  }
}


@media screen and (min-width: 1024px){
  .fancybox__slide{
    padding: 1em 2em;
  }

  .fancybox__carousel .fancybox__slide.has-iframe .fancybox__content, .fancybox__carousel .fancybox__slide.has-map .fancybox__content, .fancybox__carousel .fancybox__slide.has-pdf .fancybox__content{
    height: 95%;
  }
}


@media screen and (max-width: 992px){
  .tabs{
    position: relative;
    &._active{
      .tabs__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__current{
      padding: .5em 3em .5em 1.125em;
      display: grid;
      grid-template-columns: 3em calc(100% - 3.75em);
      align-items: center;
      gap: .75em;
      height: 4.5625em;
      border-radius: .5em;
      background-color: $grayBg;
      background-image: url(../img/arrow-bottom.svg);
      background-size: 1.0625em auto;
      background-position: right 1.25em center;
      background-repeat: no-repeat;
      margin-bottom: 1.0625em;
      .sector-card__icon{
        height: 3em;
      }
      br{
        display: none;
      }
    }
    &__dropdown{
      z-index: 10;
      margin-top: .25em;
      position: absolute !important;
      top: 100%;
      left: 0;
      width: 100%;
      grid-template-columns: 100% !important;
      gap: 0 !important;
      overflow-y: auto;
      max-height: 18em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      border-radius: .5em;
      background-color: $grayBg;
      box-shadow: .25em .4375em .8125em 0 rgba(90,91,91,.17);
      .sector-card{
        min-height: unset;
        align-items: unset;
        text-align: left;
        padding: 1em 1.125em;
        border-radius: 0;
        &:not(:hover),
        &:not(._active){
          background-color: transparent;
        }
        &__icon{
          display: none;
        }
        br{
          display: none;
        }
      }
    }
  }

  .swiper-nav{
    display: flex;
    justify-content: center;
    margin-top: .875em;
    .swiper-button{
      transform: unset;
      position: static;
      margin: 0;
      width: 2.75em;
      height: 2.75em;
      svg{
        max-width: 1.5em;
        max-height: 1.5em;
      }
    }
  }

  .grid-column6{
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
  }

  .grid-column4{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }

  .grid-column3{
    grid-template-columns: repeat(2,calc(50% - .4375em));
    gap: .875em;
  }
}


@media screen and (max-width: 576px){
  .grid-column6{
    grid-template-columns: repeat(2,calc(50% - .5em));
  }

  .grid-column3{
    grid-template-columns: 100%;
  }
}
