.direction-page{
  padding-bottom: 2.5em;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  .tab{
    height: 5em;
  }
  .product-slider{
    margin-top: 2.5em;
    @media screen and (min-width: 993px){
      .swiper{
        &-nav{
          display: flex;
          justify-content: center;
          margin-top: .875em;
          .swiper-button{
            transform: unset;
            position: static;
            margin: 0;
            width: 2.75em;
            height: 2.75em;
            svg{
              max-width: 1.5em;
              max-height: 1.5em;
            }
          }
        }
      }
    }
  }
  &__rects{
    position: absolute;
    z-index: -1;
    top: 27.5em;
    left: -6.0625em;
    width: 92.6875em;
  }
}

.direction-info{
  display: grid;
  grid-template-columns: 7.875em calc(100% - 9.125em);
  gap: 1.25em;
  &__icon{
    height: 7.875em;
    border-radius: .5em;
    background-color: #fff;
    @include center;
    svg{
      width: 5.375em;
      height: 5.375em;
      fill: $primary;
    }
    img{
      width: 5.375em;
    }
  }
  &__text{
    margin-top: .8em;
  }
}

.direction-consult{
  @include grid100gap(1em);
  margin-bottom: 1.25em;
  &__nav{
    display: flex;
  }
  &__btn{
    width: 13.125em;
    height: 3.625em;
    @include lastM(0 1.0625em 0 0);
  }
}


@media screen and (max-width: 992px){
  .direction-page{
    &__rects{
      display: none;
    }
  }

  .direction-consult{
    .text27{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .direction-info{
    grid-template-columns: 100%;
    &__icon{
      width: 7.875em;
      margin: 0;
    }
  }
}
