.basket {
  padding: 0 0 2.3125em;
  &__inner {
    position: relative;
  }
  &-bg {
    position: absolute;
    z-index: -1;
    top: -3.9375em;
    right: -2.75em;
    width: 15.25em;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.875em;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f5f5f5;
    border-radius: 0.625em;
    padding: 0.75em 2em 0.75em 0.875em;
    &-left {
      display: flex;
      align-items: center;
    }
    &-right {
      height: 100%;
      display: flex;
      align-items: center;
      margin-left: 2em;
    }
    &-img {
      flex-shrink: 0;
      width: 7.5em;
      height: 7.5em;
      padding: 0.9375em;
      background-color: #ffffff;
      border-radius: 0.3125em;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 30.625em;
      margin-left: 1.5625em;
      p {
        font-size: 16px;
        line-height: 24px;
        font-weight: 400;
        letter-spacing: 0.025em;
        color: #434242;
      }
    }
    &-price {
      span:nth-child(1) {
        display: block;
        font-size: 1.875em;
        line-height: 120%;
        font-weight: 700;
        color: #434242;
      }
      span:nth-child(2) {
        display: block;
        font-size: 1em;
        line-height: 120%;
        text-decoration: line-through;
        color: #434242;
        text-align: right;
        margin-top: 0.3125em;
      }
    }
    &-cartcalc {
      height: calc(100% + 1.5em);
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 4.375em;
      border-left: 0.0625em solid #e2e2e2;
      border-right: 0.0625em solid #e2e2e2;
      margin: 0 0 0 3.75em;
      .cartcalc {
        width: 8.25em;
        height: 3.5em;
        grid-template-columns: 2.1875em calc(100% - 4.375em) 2.1875em;
        border: 0.0625em solid #e4e4e4;
        input {
          border-left: 0.0625em solid #e4e4e4;
          border-right: 0.0625em solid #e4e4e4;
        }
      }
      .ccalc-minus {
        background: #ffffff;
      }
      .ccalc-plus {
        background: #ffffff;
      }
    }
    &-btn {
      width: 1.3125em;
      margin-left: 1.875em;
      img {
        display: block;
        width: 100%;
      }
    }
  }
  &__info {
    margin-top: 1.1875em;
    p {
      font-size: 0.875em;
      line-height: 140%;
      letter-spacing: 0.025em;
      color: #434242;
    }
  }
  &__result {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(202,84,34,0.0);
    border: 0.0625em solid #dedede;
    border-radius: 0.625em;
    padding: 1em 1em 1em 2.125em;
    margin-top: 2.3125em;
    &-left {
      display: flex;
      align-items: center;
    }
    &-right {
      display: flex;
      align-items: center;
      margin: 0 0 0 2em;
    }
    &-quantity {
      font-size: 1.125em;
      line-height: 120%;
      font-weight: 700;
      color: #434242;
    }
    &-print {
      display: flex;
      align-items: center;
      margin-left: 1.5625em;
      i {
        width: 1.25em;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 0.875em;
        line-height: 120%;
        color: #434242;
        margin-left: 0.3125em;
      }
    }
    &-price {
      display: flex;
      align-items: flex-end;
      span:nth-child(2) {
        font-size: 1.875em;
        line-height: 100%;
        font-weight: 700;
        color: #434242;
        margin-left: 0.3125em;
      }
      span:nth-child(1) {
        font-size: 1em;
        line-height: 100%;
        font-weight: 400;
        color: #434242;
      }
    }
    &-btn {
      height: 3.625em;
      border-radius: 0.5em;
      padding: 0 1.25em;
      margin: 0 0 0 1.5625em;
    }
    &-btn2 {
      height: 3.625em;
      border-radius: 0.5em;
      padding: 0 1.25em;
      margin: 0 0 0 1.5625em;
    }
  }
}

.collection-cart{
  padding: 2.25em 0 2.5em;
}


@media screen and (max-width: 992px) {
  .basket {
    &-bg {
      display: none;
    }
    &__item {
      display: block;
      padding: 0.75em 1em;
      &-right {
        height: auto;
        margin: 1em 0 0 0;
      }
      &-img {
        width: 6.25em;
        height: 6.25em;
        padding: 0.625em;
      }
      &-text {
        max-width: 100%;
      }
      &-price {
        span:nth-child(1) {
          font-size: 1.5em;
        }
      }
      &-cartcalc {
        height: 100%;
        padding: 0;
        margin: 0 0 0 1.875em;
        border: none;
        .cartcalc {
          width: 5.8125em;
          height: 2.5em;
          grid-template-columns: 1.75em calc(100% - 3.5em) 1.75em;
        }
      }
    }
    &__result {
      display: block;
      padding: 1em;
      &-right {
        margin: 1em 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .basket {
    &__item {
      &-left {
        display: block;
      }
      &-text {
        margin: 1em 0 0 0;
      }
    }
    &__result {
      &-left {
        display: block;
      }
      &-right {
        display: block;
      }
      &-print {
        margin: 1em 0 0 0;
      }
      &-btn {
        margin: 1em 0 0 0;
      }
      &-btn2 {
        margin: 1em 0 0 0;
      }
    }
  }
}
