.auth{
  padding: 1.875em 0;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  .container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__inner{
    max-width: 100%;
    width: 22.5em;
    margin: auto;
    position: relative;
  }
  &__rects{
    position: absolute;
    z-index: -1;
  }
  &__rects1{
    width: 10.9375em;
    top: 4.875em;
    right: -7.875em;
  }
  &__rects2{
    width: 7.625em;
    bottom: 1.625em;
    left: -4.3125em;
  }
}

.success-mess,
.auth-yandex,
.auth-form{
  background-color: #ffffff;
  box-shadow: .4375em .8125em 2.375em 0 rgba(35,31,32,0.12);
  border-radius: .5em;
  padding: 1.375em 1em 1.25em;
}

.auth-form{
  &__top{
    text-align: center;
  }
  &__grid{
    margin-top: .75em;
    @include grid100gap(.625em);
    .fg{
      input{
        text-align: center;
        height: 3.6em;
      }
    }
  }
  &__btn{
    width: 100%;
    height: 3.25em;
    border-radius: .3125em;
    letter-spacing: 0.025em;
  }
  &__link{
    @include center;
    button,
    a{
      text-decoration: underline;
    }
  }
}

.auth-yandex{
  margin-top: 1.875em;
}

.success-mess{
  @include grid100gap(.75em);
  text-align: center;
  &__icon{
    img {
      display: block;
      margin: 0 auto;
      width: 2.75em;
    }
  }
}


@media screen and (max-width: 992px){
  .auth{
    &__rects{
      display: none;
    }
  }
}
