.fg {
  position: relative;
  margin: 0;
  .nice-select,
  input,
  select,
  textarea{
    font-family: $font1;
    border: 1px solid $lightGrayBg;
    padding: 0 1em;
    height: 3.13em;
    width: 100%;
    font-size: .9375em;
    line-height: 140%;
    background-color: $lightGrayBg;
    border-radius: .33em;
    color: $textBlack;
    &::placeholder {
      color: $textBlack;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    background-image: url(../img/arrow-bottom.svg) !important;
    background-repeat: no-repeat !important;
    background-position: right .75em center !important;
    background-size: 1.33em auto !important;
    span{
      font-size: 1em;
    }
    .list{
      box-shadow: 0.0625em 0.1875em 0.8125em 0 rgba(0,0,0,.2);
      border-radius: 0.3125em;
      padding: 1em 1.125em;
      @include grid100gap(.75em);
    }
    .option{
      border: none !important;
      padding: 0;
      background-color: transparent !important;
      font-weight: 400 !important;
      font-size: 1em;
      color: #727171;
      &:hover{
        color: $black;
      }
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/arrow-bottom.svg);
    background-repeat: no-repeat;
    background-position: right .75em center;
    background-size: 1.33em auto;
    &::-ms-expand {
      display: none; // IE
    }
  }
  textarea{
    height: 9.5em;
    resize: none;
    padding-top: 1em;
    padding-bottom: 1em;
  }
  label{
    font-size: .9375em;
    line-height: 120%;
    letter-spacing: 0.025em;
  }
  &__error{
    display: none;
    color: $primary;
    margin-top: .25em;
    .text12{
      line-height: 117%;
    }
  }
  &._error{
    input{
      border-color: $primary;
    }
    .fg__error{
      display: block;
    }
  }
}

.error-item{
  display: none;
  &._active{
    display: block;
  }
}


.m-radio,
.m-check {
  position: relative;
  letter-spacing: 0.025em;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:disabled+label {

    }
  }
  label {
    padding-left: 1.8125em;
    position: relative;
    min-height: 1.1875em;
    cursor: pointer;
    display: block;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      background-color: #ffffff;
      width: 1.1875em;
      height: 1.1875em;
      left: 0;
      top: 0;
      background-size: .625em auto;
      background-position: center;
      background-repeat: no-repeat;
    }
    span{
      font-size: 1em;
      line-height: 118.75%;
    }
    a{
      position: relative;
      z-index: 3;
      text-decoration: underline;
      color: $primary;
    }
  }
}

.m-check{
  input{
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-image: url(../img/check4.png);
      }
    }
  }
  label{
    &:before{
      border-radius: .3125em;
      border: 1px solid #e1e1e1;
    }
  }
}

.m-radio{
  input{
    &:checked+label {
      &::after {
        opacity: 1;
      }
    }
    &:disabled{
      pointer-events: none;
      &+label{
        color: #a1a1a1;
      }
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-color: #ececec;
    }
    &:after{
      content: '';
      position: absolute;
      width: .5625em;
      height: .5625em;
      top: .3125em;
      left: .3125em;
      background-color: $primary;
      border-radius: 50%;
      z-index: 2;
      opacity: 0;
    }
  }
  &__hint{
    margin-top: .25em;
    padding-left: 1.8125em;
    color: #8c8c8c;
    max-width: 20em;
    &._error{
      color: #ea5626;
    }
    span{
      font-size: .625em;
      line-height: 130%;
    }
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.125em;
    height: 1.125em;
    margin: 0 .3125em 0 0;
    svg{
      max-width: 100%;
      max-height: 100%;
      fill: #c3c3c3;
    }
    &:hover {
      cursor: pointer;
      svg{
        fill: $primary;
      }
      &~.rating-input__item {
        svg{
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  svg{
    fill: $primary;
  }
}

.file-input{
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  height: 3em;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
  }
  svg{
    width: 1.25em;
    height: 1.25em;
    margin-right: .625em;
    fill: $primary;
  }
  span{
    font-size: 1em;
    line-height: 120%;
    letter-spacing: 0.025em;
  }
}


@media screen and (max-width: 992px){
  .rating-input{
    &__item{
      width: .875em;
      height: .875em;
      margin-right: .125em;
    }
  }

  .file-input{
    height: unset;
  }
}
