.catalog{
  padding-bottom: 1.125em;
  &__inner{
    display: grid;
    grid-template-columns: 18.3125em calc(100% - 20.5625em);
    gap: 2.25em;
    position: relative;
  }
  &__banners{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
    &:first-child{
      margin-top: 0;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.5em 1.25em;
    margin-top: 1.25em;
    .sector-card{
      min-height: 14.6875em;
      display: flex;
      flex-direction: column;
      gap: unset;
      background-color: $grayBg;
      text-align: center;
      padding: 2.25em 1.5em;
      &__icon{
        height: 4.375em;
        margin: 0 0 .875em;
      }
      .text18{
        hyphens: unset;
      }
    }
  }
  &__rects,
  &__rects2,
  &__rects3,
  &__rects4,
  &__rects5{
    position: absolute;
    z-index: -1;
  }
  &__rects{
    top: 15.1875em;
    left: -4.5em;
    width: 7.625em;
  }
  &__rects2{
    top: 10em;
    right: -3.0625em;
    width: 15.25em;
  }
  &__rects3{
    bottom: 10.1875em;
    right: 13.625em;
    width: 15.25em;
  }
  &__rects4{
    width: 15.25em;
    top: 1.0625em;
    right: -3.0625em;
  }
  &__rects5{
    width: 15.25em;
    top: 10em;
    right: 8.875em;
  }
}

.catalog-banner{
  border-radius: .5em;
  background-size: cover;
  background-position: center;
  min-height: 17.5em;
  padding: 1.125em .75em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  color: #fff;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    border-radius: inherit;
    width: 100%;
    height: 100%;
    background-color: $black;
    opacity: .4;
  }
  &>*{
    position: relative;
    z-index: 2;
  }
  &__btn{
    margin-top: .625em;
    width: 100%;
    height: 3.8125em;
    border-radius: .3125em;
    span{
      font-size: 1.125em;
    }
  }
}

.catalog2{
  padding: 1.125em 0 2em;
  &__nav{
    margin-top: 1.25em;
    display: flex;
    justify-content: center;
    .text-link{
      color: $textBlack;
    }
  }
  &__title{
    text-align: center;
  }
  &__ceo{
    position: relative;
    margin-top: 1.75em;
    @include grid100gap(.5em);
    p{
      line-height: inherit;
    }
    &-desc{
      @include grid100gap(1.5em);
    }
  }
  .catalog__content{
    padding-top: 3.625em;
  }
  &__rects,
  &__rects2{
    position: absolute;
    width: 15.25em;
    z-index: -1;
  }
  &__rects{
    top: -.875em;
    right: -3.0625em;
  }
  &__rects2{
    right: -3.0625em;
    top: -6.5em;
  }
}

.news-card2{
  border-radius: .625em;
  background-color: $grayBg;
  &__img{
    @include imgCover(61.4%);
    img{
      border-radius: .625em .625em 0 0;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      border-radius: .625em .625em 0 0;
      width: 100%;
      height: 100%;
      background-color: $black;
      opacity: .4;
      z-index: 2;
    }
  }
  &__top{
    position: relative;
  }
  &__info{
    position: absolute;
    padding: .875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 3;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  &__item{
    display: flex;
    align-items: center;
    color: #ffffff;
    img{
      width: 1.0625em;
      margin-right: .4375em;
      flex-shrink: 0;
    }
  }
  &__content{
    padding: 1em .75em;
    text-align: center;
  }
  &__nav{
    margin-top: auto;
    padding: .625em .3125em 0;
  }
}

.catalog-help{
  padding: 1.0625em 1.0625em 1.0625em 2.125em;
  border-radius: .625em;
  background-color: #c4c2c0;
  background-image: linear-gradient(90.0deg, #ea5626 0%, #fc6d3f 100%);
  display: grid;
  grid-template-columns: calc(100% - 29.25em) 3.375em 19.625em;
  gap: 3.125em;
  align-items: center;
  margin-top: 2em;
  &:first-child{
    margin-top: 0;
  }
  &__text{
    color: #fff;
    @include grid100gap(.5em);
  }
  &__arrow{
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    @include grid100gap(.625em);
  }
  &__btn{
    height: 4.0625em;
    border-radius: .5em;
    span{
      font-size: 1em;
      line-height: 150%;
    }
    img{
      width: 1.625em;
      margin-right: .625em;
      flex-shrink: 0;
    }
  }
}

.catalog-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border: $border;
  border-radius: .625em;
  background-color: #ffffff;
  padding: .25em .5em 1.25em;
  &:hover{
    box-shadow: 0 .0625em 2.6875em 0 rgba(0,0,0,0.18);
    border-color: #fff;
    color: $textBlack;
  }
  &__img{
    height: 12em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{

  }
}

.news-sidebar{
  margin-top: 1.25em;
  &:first-child{
    margin-top: 0;
  }
  .news-card2{
    margin-top: 1.25em;
  }
}

.catalog3{
  padding-bottom: 3em;
  .equip2__btn{
    width: 15.5em;
    max-width: 100%;
  }
}

.catalog-spoiler{
  border-radius: .625em;
  background-color: $lightGrayBg;
  margin-bottom: 1.25em;
  &._active{
    @media screen and (min-width: 993px){
      .catalog-spoiler__btn{
        svg{
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__show{
    padding: 1em .875em 1em 1.625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    width: 2.125em;
    height: 2.125em;
    background-color: #fff;
    border-radius: .3125em;
    @include center;
    svg{
      width: 1.125em;
      height: auto;
    }
  }
  &__hidden{
    padding: 1.25em 0;
    border-top: 1px solid #e2e2e2;
    display: none;
  }
}

.catalog-nav{
  &__items{
    padding-left: .875em;
    padding-right: 1.5em;
    @include grid100gap(1.25em);
  }
}

.nav-item{
  &._active{
    .nav-item__btn{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &._type2{
    &:hover{
      .nav-item__icon{
        svg{
          fill: $primary;
        }
      }
    }
    .nav-item__show{
      align-items: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
    &:hover{
      .nav-item__icon{
        svg{
          fill: $primary;
        }
      }
    }
    &._center{
      align-items: center !important;
    }
  }
  &__icon{
    margin-right: .625em;
    width: 2.125em;
    height: 1.875em;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    svg{
      width: 100%;
      height: 100%;
      transition: all .1s;
    }
  }
  &__title{
    font-size: .875em;
    line-height: 130%;
  }
  &__btn{
    margin-left: auto;
    padding-left: .25em;
    flex-shrink: 0;
    svg{
      width: 1.25em;
      height: auto;
    }
  }
  &__hidden{
    padding: .75em 0 0 2.75em;
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    color: #6b6b6b;
    @include lastM(0 0 .5em);
    &:hover{
      color: $black;
    }
    span{
      font-size: .875em;
      line-height: 130%;
    }
  }
}

.filter{
  .catalog-spoiler__hidden{
    padding: 0;
  }
  &__bg{
    display: none;
  }
  &__top{
    margin-bottom: .625em;
  }
  &__slider{
    padding-bottom: .375em;
    &-inputs{
      display: grid;
      grid-template-columns: calc(50% - .5em) 1em calc(50% - .5em);
      align-items: center;
    }
    &-split{
      text-align: center;
      letter-spacing: 0.025em;
      font-size: 1em;
    }
    .fg{
      input{
        background-color: #fff;
        font-size: 1em;
        height: 3.0625em;
        padding: 0 2em 0 1em;
        letter-spacing: 0.025em;
        border-radius: .3125em;
      }
      span{
        position: absolute;
        right: .67em;
        font-size: 1.125em;
        line-height: 120%;
        top: 50%;
        transform: translateY(-50%);
        color: #c1c1c1;
      }
    }
  }
  &__check{
    display: flex;
    align-items: center;
  }
  &__checks{
    @include grid100gap(.5em);
  }
  &__check{
    display: flex;
    align-items: center;
    min-height: 1.5em;
  }
  &__badge{
    display: flex;
    align-items: center;
    padding: 0 .75em;
    min-height: 1.5em;
    border-radius: .75em;
    background-color: #fff;
    margin-left: .875em;
    span{
      letter-spacing: 0.025em;
    }
  }
}

.filter-group{
  padding: .875em 1.25em;
  border: none;
  &:not(:first-child){
    border-top: 1px solid #e2e2e2;
  }
}

.range-slider{
  width: calc(100% - 1em);
  margin: .875em auto 0;
  background-color: #ffffff;
  border-radius: .125em;
  border: none;
  height: .25em;
  .noUi-connect{
    background-color: $primary;
    background-image: linear-gradient(90.0deg, #ea5626 0%, #ea9026 100%);
  }
  .noUi-handle{
    border-radius: 50%;
    width: 1em;
    height: 1em;
    box-shadow: .0625em .125em .375em 0 rgba(0,0,0,0.28);
    background-color: $primary;
    border: none;
    right: -.5em;
    top: -.375em;
    &:before,
    &:after{
      display: none;
    }
  }
}

.catalog-sort{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__select{
    width: 15em;
  }
  &__filter-toggle{
    display: none;
  }
}

.catalog5{
  .catalog-help{
    margin-top: 3.75em;
  }
  @media screen and (min-width: 993px){
    .sector-card{
      .text18{
        font-size: 1.25em;
        line-height: 125%;
      }
    }
  }
}

.mob-nav{
  display: none;
}


@media screen and (max-width: 992px){
  .catalog{
    &__inner{
      grid-template-columns: 100%;
      gap: 1em;
      position: relative;
    }
    &__sidebar{
      order: 2;
      margin-top: .5em;
    }
    &__banners{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
      .sector-card{
        min-height: 12em;
      }
    }
    &__rects,
    &__rects2,
    &__rects3,
    &__rects4,
    &__rects5{
      display: none;
    }
  }

  .catalog2{
    .catalog__content{
      padding: 0;
    }
    .catalog__sidebar{
      order: unset;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .catalog-help{
    grid-template-columns: 100%;
    gap: 1em;
    padding: 1em;
    &__arrow{
      display: none;
    }
  }

  .catalog-sort{
    display: grid;
    grid-template-columns: 100%;
    &__select{
      height: 3em;
      width: 100%;
      select{
        height: 100%;
      }
    }
    ._full{
      grid-column: 1/-1;
    }
    &__filter-toggle{
      background-color: $lightGrayBg;
      @include center;
      height: 4em;
      width: 4em;
      border-radius: .5em;
      img{
        width: 1.6875em;
      }
    }
  }

  .catalog-nav{
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 4.625em);
    z-index: 2;
    background-color: transparent;
    .catalog-spoiler{
      &__show{
        border-radius: .625em;
        background-color: $lightGrayBg;
      }
      &__hidden{
        box-shadow: 0 .25em 1.875em rgba(0, 33, 51, 0.3);
        background-color: $lightGrayBg;
        border-radius: .625em;
        display: block !important;
        border-top: none;
        position: absolute;
        top: 100%;
        left: 0;
        width: calc(100% + 4.625em);
        margin-top: .625em;
        opacity: 0;
        pointer-events: none;
        transition: all .3s;
      }
    }
    &._active2{
      .catalog-spoiler__btn{
        svg{
          transform: rotate(-180deg);
        }
      }
      .catalog-spoiler__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .catalog-nav2{
    width: 100%;
    .catalog-spoiler__hidden{
      width: 100%;
    }
  }

  .mob-nav{
    display: flex;
    justify-content: flex-end;
    min-height: 4em;
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    height: $fullHeight;
    width: 18.3125em;
    z-index: 21;
    border-radius: 0;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transform: translateX(-200%);
    transition: all .5s;
    &._show{
      opacity: 1;
      transform: translateX(0);
      pointer-events: unset;
      &~.filter__bg{
        display: block;
      }
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $fullHeight;
      z-index: 20;
      background: rgba($black,.7);
    }
    .catalog-spoiler{
      &__show{
        pointer-events: none;
      }
      &__btn{
        display: none;
      }
      &__hidden{
        display: block !important;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__banners{
      grid-template-columns: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      .sector-card{
        padding: 1em;
        min-height: 10em;
        &__icon{
          height: 4em;
        }
      }
    }
  }
}
