.order{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 18.375em) 16.5em;
    gap: 1.875em;
    position: relative;
  }
  &__rects{
    position: absolute;
    width: 7.625em;
    z-index: -1;
    left: -4.35em;
    bottom: 19.25em;
  }
  &__content{
    padding: 1.75em 2.25em;
    background-color: $lightGrayBg;
    border-radius: .625em;
  }
  .review{
    &__top{
      .text{
        margin-top: .5em;
      }
    }
  }
  .review-form{
    margin-top: 1em;
  }
  &__nav{
    display: flex;
    margin-top: 1.125em;
  }
  &__btn{
    padding: 0 2em;
    height: 3.75em;
    flex-shrink: 0;
  }
  &__confirm{
    margin-left: 1.125em;
    .m-check{
      letter-spacing: unset;
      label{
        span{
          font-size: .875em;
          line-height: 150%;
        }
      }
    }
  }
}

.order-detail{
  margin-top: 1.625em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1em));
  gap: 2em;
  &__col{
    padding: 1em;
    background-color: #fff;
    border-radius: 1.375em .625em .625em;
    display: flex;
    flex-direction: column;
  }
  &__top{
    padding: 0 1.125em;
    margin-bottom: .75em;
  }
  &__radios{
    margin-top: 1.375em;
    @include grid100gap(.875em);
  }
  &__bottom{
    margin: auto 0 0;
    padding: .875em 1.125em;
    border-radius: .3125em;
    background-color: $lightGrayBg;
    .order-detail__radios{
      margin-top: .5em;
    }
    .m-radio{
      label:before{
        background-color: #fff;
      }
    }
  }
}

.order-result{
  position: sticky;
  top: 1em;
  background-color: $lightGrayBg;
  border-radius: .625em;
  &__top{
    padding: .5em 1.375em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 3.625em;
    border-bottom: 1px solid #e4e4e4;
    letter-spacing: 0.025em;
  }
  &__edit{
    display: flex;
    align-items: center;
    &:hover{
      color: $primary;
    }
    img{
      width: .875em;
      flex-shrink: 0;
      margin-right: .3125em;
    }
    span{
      font-size: .8125em;
      line-height: 120%;
    }
  }
  &__bottom{
    padding: .75em 1.25em 1.25em;
  }
  &__props{
    @include grid100gap(.375em);
  }
  .prop-item{
    &:before{
      bottom: .5em;
    }
    &__title:after{
      display: none;
    }
    &__desc{
      span{
        font-size: 1.125em;
        font-weight: 700;
      }
    }
  }
  &__nav{
    margin-top: 1em;
  }
  &__btn{
    width: 100%;
    height: 3.75em;
  }
  &__confirm{
    margin-top: .75em;
    .m-check{
      label{
        span{
          font-size: .75em;
          line-height: 117%;
        }
      }
    }
  }
}

.order-confirm{
  padding: 1.75em 0 2.75em;
  &__inner{
    margin: 0 auto;
    width: 47em;
    max-width: 100%;
    position: relative;
  }
  &__wrap{
    background-color: $lightGrayBg;
    border-radius: .625em;
    padding: 2em 2em 2.3125em;
    text-align: center;
  }
  .success-mess__icon{
    margin-bottom: .875em;
    img{
      width: 3.125em;
    }
  }
  .text16{
    line-height: 187.5%;
    margin-top: .75em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.125em;
  }
  &__btn{
    width: 20em;
    max-width: 100%;
    height: 4em;
    border-radius: .3125em;
  }
  &__rects{
    position: absolute;
    z-index: -1;
  }
  &__rects1{
    width: 16.75em;
    top: -2.9375em;
    right: -2.9375em;
  }
  &__rects2{
    width: 7.625em;
    bottom: 2em;
    left: -4.375em;
  }
}



@media screen and (max-width: 992px){
  .order{
    padding-bottom: 1.625em;
    &__inner{
      grid-template-columns: 100%;
      gap: .875em;
    }
    &__rects{
      display: none;
    }
    &__content{
      border-radius: 0;
      padding: 0;
      background-color: #fff;
    }
    &__nav{
      flex-direction: column;
      align-items: flex-start;
    }
    &__confirm{
      margin: 0 0 .75em;
      order: -1;
    }
    &__sidebar{
      order: -1;
      margin-top: -.5em;
    }
    .review__top{
      padding: 1.25em 1.5em 1.5em;
      border-radius: .625em;
      background-color: $lightGrayBg;
      .text27{
        font-size: 1.5em;
      }
    }
    .review-form{
      background-color: $lightGrayBg;
      margin-top: .75em;
      padding: 1.5em 1.5em 1em;
      &__grid{
        gap: .625em;
        margin: 0;
      }
      .fg{
        input,
        textarea{
          background-color: #fff;
        }
        input{
          height: 3.5em;
        }
      }
    }
  }

  .order-result{
    .prop-item{
      display: flex;
      &:before{
        display: block;
      }
      &__title{
        padding: 0 .25em 0 0;
        color: unset;
      }
      &__desc{
        padding-left: .25em;
      }
      &__title,
      &__desc{
        font-size: 1em;
      }
    }
    .order-result__confirm{
      display: none;
    }
    &__btn{
      height: 3.5em;
    }
  }

  .order-detail{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    margin-top: 1em;
    &__col{
      background-color: $lightGrayBg;
    }
    &__top{
      padding: 0 .625em;
      .m-radio{
        label:before{
          background-color: #fff;
        }
      }
    }
    &__bottom{
      background-color: #fff;
      .m-radio{
        label:before{
          background-color: $lightGrayBg;
        }
      }
    }
  }

  .order-confirm{
    &__rects{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    &__btn{
      width: 100%;
    }
  }

  .order-detail{
    grid-template-columns: 100%;
  }

  .order-confirm{
    padding-top: 0;
    &__wrap{
      padding: 1em;
    }
  }
}
