.lk{
  padding-bottom: 3em;
  &__inner{
    display: grid;
    grid-template-columns: 18.3125em calc(100% - 20.5625em);
    gap: 2.25em;
    position: relative;
  }
  &__content{
    position: relative;
  }
  .order-result__edit{
    letter-spacing: 0.025em;
    position: absolute;
    bottom: 100%;
    right: 0;
    height: 3.3125em;
    img{
      width: 1.0625em;
    }
    span{
      font-size: .875em;
    }
  }
}

.lk-sidebar{
  background-color: $lightGrayBg;
  border-radius: .625em;
  &__top{
    min-height: 4.25em;
    display: flex;
    align-items: center;
    padding: .5em 1.625em;
    border-bottom: 1px solid #e2e2e2;
  }
  &__bottom{
    padding: 1.25em 1.625em;
  }
  &__links{
    @include grid100gap(1.5em);
  }
  &__link{
    display: flex;
    align-items: center;
    &:hover{
      color: $black;
    }
    &-icon{
      width: 1.75em;
      height: 1.5em;
      margin-right: .625em;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
    }
    svg{
      max-width: 100%;
      max-height: 100%;
    }
    path{
      fill: $primary;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
    ._view2{
      display: none;
    }
  }
}

.lk-table{
  letter-spacing: 0.025em;
  padding: .875em .875em 1em;
  border-radius: .625em;
  background-color: $lightGrayBg;
  table{
    width: 100%;
    border-spacing: 0;
  }
  th{
    height: 2.75em;
    text-align: left;
    span{
      font-size: 1em;
      font-weight: 700;
    }
  }
  th,
  td{
    &:first-child{
      padding-left: 1.25em;
    }
    &:not(:last-child){
      padding-right: .5em;
    }
    &:last-child{
      padding-right: 1.25em;
      max-width: 15em;
    }
  }
  td{
    background-color: #fff;
    padding-top: .8125em;
    padding-bottom: .8125em;
    vertical-align: top;
    &:first-child{
      border-top-left-radius: .3125em;
      border-bottom-left-radius: .3125em;
    }
    &:last-child{
      border-top-right-radius: .3125em;
      border-bottom-right-radius: .3125em;
    }
  }
  &__title{
    display: none;
  }
}

.lk-orders{
  @include grid100gap(3em);
}

.lk-order{
  background-color: $lightGrayBg;
  border-radius: .625em;
  position: relative;
  &__top{
    padding: .5em .875em .5em 1.375em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 3.75em;
  }
  &__info{
    flex-grow: 1;
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 2.125em;
    height: 2.125em;
    margin-left: 1.5em;
    flex-shrink: 0;
    @include center;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__count{
    margin-left: 1em;
  }
  &__price{
    display: flex;
    align-items: flex-start;
    margin-left: auto;
    &-num{
      font-size: 1.5625em;
      font-weight: 700;
      margin-left: .5em;
      line-height: 85%;
    }
  }
  &__status{
    position: absolute;
    padding-right: .625em;
    bottom: 100%;
    right: 0;
    margin-bottom: .625em;
    display: flex;
    align-items: center;
    letter-spacing: 0.025em;
    &-item{
      margin-left: 1.25em;
      display: flex;
      align-items: center;
    }
    img{
      width: 1.375em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__bottom{
    padding: .75em 0 .5em;
    border-top: 1px solid #e4e4e4;
    display: none;
  }
  &__subtitle{
    padding: 0 1.375em;
  }
  &._active{
    .lk-order__btn{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.order-item{
  padding: .625em 4.375em .625em 1.375em;
  display: flex;
  align-items: center;
  &:not(:first-child){
    border-top: 1px solid #e4e4e4;
  }
  &__img{
    width: 4.5625em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    @include truncate(2);
  }
  &__price{
    padding-left: 1em;
    font-size: 1.5625em;
    font-weight: 700;
    white-space: nowrap;
  }
}

.edit{
  padding: 1.25em;
  background-color: $lightGrayBg;
  border-radius: .625em;
  &__top{
    display: flex;
  }
  &__back{
    display: flex;
    align-items: center;
    img{
      width: 1.3125em;
      display: block;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__grid{
    margin-top: .75em;
    display: grid;
    grid-template-columns: calc(44% - .625em) calc(56% - .625em);
    gap: .875em 1.25em;
  }
  &__password{
    display: flex;
    &-btn{
      height: 3.1875em;
      padding: 0 1.125em;
    }
  }
  .fg{
    display: grid;
    gap: .5em;
    align-items: center;
    input{
      background-color: #fff;
      height: 3.4em;
    }
    @media screen and (min-width: 993px){
      grid-template-columns: 2.75em calc(100% - 3.25em);
      &:nth-child(even){
        grid-template-columns: 9.5em calc(100% - 10em);
      }
      label{
        text-align: right;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.125em;
  }
  &__btn{
    padding: 0 2em;
    height: 3.75em;
    flex-shrink: 0;
  }
  &__confirm{
    max-width: 26.875em;
    margin-left: 1.125em;
    .m-check{
      letter-spacing: unset;
      label{
        span{
          font-size: .875em;
          line-height: 150%;
        }
      }
    }
  }
}



@media screen and (max-width: 992px){
  .lk{
    padding-bottom: 1.75em;
    &__inner{
      grid-template-columns: 100%;
      gap: 3em;
      margin-top: -.875em;
    }
    .order-result__edit{
      height: 3em;
      right: unset;
      left: 0;
    }
  }

  .lk-sidebar{
    max-width: 22.5em;
    padding: 0;
    background-color: transparent;
    &__top{
      display: none;
    }
    &__bottom{
      padding: 0;
    }
    &__links{
      grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
      gap: .75em;
    }
    &__link{
      height: 5em;
      background-color: $lightGrayBg;
      flex-direction: column;
      border-radius: .625em;
      padding: 1.25em .5em;
      text-align: center;
      &._active{
        background-color: $primary;
        color: #fff;
        path{
          fill: #fff;
        }
      }
      &-icon{
        margin: 0 0 .625em;
      }
      span{
        font-size: .75em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .lk-table{
    padding: 1.125em 1.75em;
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      display: block;
    }
    tr{
      @include grid100gap(.75em);
    }
    td{
      padding: 0 !important;
      background-color: transparent;
      max-width: unset !important;
    }
    &__title{
      display: block;
      font-weight: 700;
    }
  }

  .lk-orders{
    gap: 4em;
  }

  .lk-order{
    &__top{
      align-items: unset;
    }
    &__info{
      @include grid100gap(.5em);
    }
    &__count,
    &__price{
      margin: 0;
    }
  }

  .order-item{
    padding-right: 1.375em;
  }
}


@media screen and (max-width: 576px){
  .order-item{
    display: grid;
    grid-template-columns: 4em calc(100% - 4.5em);
    gap: .5em;
    &__img{
      width: 100%;
      margin: 0;
    }
    &__title{
      order: 2;
      grid-column: 1/-1;
    }
    &__price{
      text-align: right;
    }
  }

  .edit{
    &__grid{
      grid-template-columns: 100%;
    }
    &__nav{
      flex-direction: column;
      align-items: flex-start;
    }
    &__confirm{
      margin: 0 0 .75em;
      order: -1;
    }
  }
}
