.maintenance-collection,
.services3{
  .services__grid{
    margin-top: 1.5em;
  }
  .service-card{
    transition: all .3s;
    &:hover{
      background-color: $textBlack;
      svg{
        fill: $primary;
      }
    }
  }
}

.maintenance-collection{
  padding-bottom: 2.5em;
  &__inner{
    position: relative;
  }
  &__rects{
    position: absolute;
    z-index: -1;
    top: -2.25em;
    right: 17.0625em;
    width: 17.75em;
  }
}

.contract{
  padding: 3.125em 0;
  background-color: $textBlack;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: 34.75em calc(100% - 36.625em);
    gap: 1.875em;
    align-items: flex-start;
  }
  &__rects{
    position: absolute;
    top: 11em;
    left: 13.75em;
    width: 7.625em;
  }
}

.contract-tariff,
.maintenance-contract{
  position: relative;
  z-index: 2;
  padding: 1.75em 2em;
  border-radius: .625em;
  background-color: $lightGrayBg;
  &__top{
    @include grid100gap(.75em);
  }
  &__desc{
    letter-spacing: 0.025em;
  }
}

.maintenance-contract{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__what{
    display: inline-flex;
    align-items: center;
    padding: .875em;
    background-color: #fff;
    border-radius: .3125em;
    margin-top: .75em;
    position: relative;
    &:hover{
      .faq-card__tooltip{
        opacity: 1;
        pointer-events: unset;
      }
    }
    img{
      width: 1.375em;
      flex-shrink: 0;
      margin-right: .3125em;
    }
    span{
      letter-spacing: 0.025em;
    }
    .faq-card__tooltip{
      margin-left: .875em;
    }
    .faq-card__hint{
      padding: 1.25em 1.375em;
      width: 38.5625em;
    }
  }
  &__what2{
    margin-top: 1.5em;
  }
  &__grid{
    margin-top: .75em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: .875em 1em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
    width: 100%;
  }
  &__btn{
    height: 3.8125em;
    width: 100%;
  }
}

.maintenance-what{
  padding: .875em 1em;
  text-align: center;
  background-color: #fff;
  border-radius: .3125em;
  &__icon{
    height: 3.5em;
    @include center;
    margin-bottom: .375em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__title{
    letter-spacing: 0.025em;
    hyphens: auto;
  }
}

.contract-nav{
  position: absolute;
  top: 3.125em;
  right: 0;
  width: 11.25em;
  z-index: 2;
  @include grid100gap(.625em);
  &__tab{
    min-height: 2.25em;
    background-color: $grayBg;
    width: calc(100% - 3em);
    margin-left: auto;
    display: flex;
    align-items: center;
    padding: .625em 1.125em;
    border-radius: .625em 0 0 .625em;
    transition: all .3s;
    cursor: pointer;
    &._active,
    &:hover{
      color: #fff;
      background-color: $primary;
    }
    &._active{
      width: 100%;
      pointer-events: none;
    }
  }
}

.contract-tariff{
  &__groups{
    margin-top: 1em;
    @include grid100gap(1em);
  }
  &__group{
    @include grid100gap(.75em);
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  &__btn{
    height: 3.875em;
    flex-grow: 1;
    span{
      font-size: 1.125em;
    }
  }
  &__price{
    display: flex;
    align-items: flex-end;
    margin-left: 1.125em;
    &-num{
      font-size: 2.25em;
      font-weight: 700;
    }
    &-text{
      margin-left: .25em;
    }
  }
}

.check-list{
  @include grid100gap(.5em);
  li{
    position: relative;
    font-size: 1em;
    line-height: 150%;
    letter-spacing: 0.025em;
    padding-left: 1.6875em;
    &:before{
      content: '';
      position: absolute;
      top: .15625em;
      left: 0;
      width: 1.1875em;
      height: 1.1875em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
    }
  }
}

.maintenance2{
  .block-info{
    text-align: left;
  }
  .maintenance__rects{
    top: 9.75em;
  }
}

.services3{
  padding: 2.25em 0 2.75em;
  .block-info{
    text-align: left;
  }
  .service-card{
    position: relative;
    &:not(:hover){
      background-color: #fff;
    }
    &:hover{
      .faq-card__tooltip{
        opacity: 1;
        pointer-events: unset;
      }
    }
    .faq-card__tooltip{
      color: $textBlack;
      top: 95%;
      left: 50%;
      transform: translate(-50%,1em);
    }
    .faq-card__hint{
      padding: 1.375em;
      width: 18.9375em;
      &:before{
        right: 50%;
        top: 0;
        width: 1.125em;
        height: 1.125em;
      }
    }
  }
  &__consult{
    border-radius: .3125em;
    background-color: $primary;
    color: #fff;
    text-align: center;
    padding: .625em;
    display: flex;
    flex-direction: column;
    &-info{
      @include grid100gap(.25em);
    }
    &-nav{
      margin-top: auto;
      padding-top: .625em;
    }
  }
  &__btn{
    width: 100%;
    height: 3.5625em;
  }
}

.seo{
  padding: 2.5em 0;
  border-top: 1px solid #b6b6b6;
  &__text{
    margin-top: 1em;
    column-count: 2;
    column-gap: 2em;
    p{
      font-size: 1.0625em;
      line-height: 141%;
      @include lastM(0 0 1em);
    }
  }
}



@media screen and (max-width: 1700px){
  .contract{
    &__inner{
      grid-template-columns: 34.75em calc(100% - 41.5em) 11.25em;
    }
  }
}



@media screen and (max-width: 992px){
  .maintenance-collection{
    padding-bottom: 1em;
    &__rects{
      display: none;
    }
  }

  .contract{
    padding: 1.5em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__tariffs{
      order: 2;
    }
    &__rects{
      display: none;
    }
  }

  .contract-nav{
    position: static;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: unset;
    &__tab{
      margin: .5em .5em 0 0;
      width: unset !important;
      border-radius: .5em;
      br{
        display: none;
      }
    }
  }

  .seo{
    padding: 1.5em 0;
    &__text{
      column-count: 1;
    }
  }
}


@media screen and (max-width: 576px){
  .maintenance-contract{
    padding: 1em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
  }

  .maintenance-what{
    padding: .75em;
    &__text{
      font-size: .75em;
    }
  }

  .contract-tariff{
    padding: 1em;
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      margin-top: 1em;
      width: 100%;
      order: 2;
    }
    &__price{
      margin: 0;
    }
  }
}
