.comparison-page{
  padding-bottom: 1.375em;
  &__groups{
    @include grid100gap(1.875em);
  }
}

.comparison-group{
  &:not(:last-child){
    border-bottom: 1px solid #d7d7d7;
  }
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__area{
    margin-top: -1.625em;
    padding-bottom: 2em;
    padding-top: 3em;
    padding-left: calc((100vw - 1600px) / 2 + 8.25em);
    //padding-right: calc((100vw - 1600px) / 2 + 8.25em);
    overflow-x: auto;
    overflow-y: hidden;
    @media screen and (max-width: 1600px){
      padding-left: 8.25em;
    }
  }
  &__items{
    display: flex;
    margin-bottom: 1em;
  }
}

.comparison-item{
  flex-shrink: 0;
  width: calc(1336px / 5 - 1em);
  margin-right: 1.25em;
  @media screen and (max-width: 1600px){
    width: calc((100vw - 18.5em) / 5 - 1em);
  }
  &:last-child{
    margin-right: 0;
  }
}

.comparison-row{
  display: inline-block;
  &__top{
    height: 2.375em;
    max-width: 100vw;
    position: sticky;
    left: 0;
    @include center;
  }
  &__grid{
    display: flex;
    text-align: center;
  }
  .comparison-item{
    padding: .25em;
    min-height: 3.3125em;
    background-color: $lightGrayBg;
    position: relative;
    @include center;
    &:nth-child(1){
      border-top-left-radius: .5em;
      border-bottom-left-radius: .5em;
    }
    &:last-child{
      border-top-right-radius: .5em;
      border-bottom-right-radius: .5em;
    }
    &:not(:last-child){
      &:before{
        content: '';
        position: absolute;
        width: 1.5em;
        height: 100%;
        left: 99%;
        top: 0;
        background-color: inherit;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .comparison-group{
    &__top{
      display: block;
    }
    .favourite-group__btn{
      margin-top: 1em;
    }
    &__area{
      padding-left: 1.875em;
      padding-right: 1.875em;
    }
  }

  .comparison-item{
    width: 14.25em;
    margin-right: .75em;
  }
}
