.cases{
  .tab{
    flex-grow: 1;
    white-space: nowrap;
    padding-left: .5em;
    padding-right: .5em;
  }
  &__content{
    border-radius: 0 0 .5em .5em;
    margin-top: -.5em;
    padding: 1.875em 2.25em;
    background-color: $lightGrayBg;
    position: relative;
    min-height: 14em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(7,calc(100% / 7 - 11.25em / 7));
    gap: 1.875em;
  }
  &__rects{
    position: absolute;
    top: 19.875em;
    left: -4.5em;
    width: 7.625em;
    z-index: -1;
  }
  &__rects2{
    position: absolute;
    top: 4.25em;
    right: -6.625em;
    width: 15.25em;
    z-index: -1;
  }
}

.tabs{
  &._type2{
    @media screen and (min-width: 993px){
      .tabs__dropdown{
        display: flex;
        align-items: center;
      }
    }
  }
}

.tab{
  padding: 0 1.875em;
  height: 3.5em;
  @include center;
  border-radius: .5em .5em 0 0;
  transition: all .3s;
  cursor: pointer;
  color: $textGray;
  &._active,
  &:hover{
    background-color: $lightGrayBg;
    color: $textBlack;
  }
  &._active{
    pointer-events: none;
  }
  svg{
    width: 2.875em;
    height: 2.875em;
    fill: $primary;
    margin-right: .75em;
  }
}

.case-card{
  padding: 1.25em .25em .75em;
  background-color: #fff;
  border-radius: .5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: all .3s;
  &:hover{
    background-color: $primary;
    color: #fff;
    cursor: pointer;
    svg{
      fill: #fff;
    }
  }
  &__icon{
    height: 4.625em;
    width: 4.625em;
    margin-bottom: .625em;
    display: flex;
    justify-content: center;
    svg{
      height: 100%;
      width: 100%;
      fill: $primary;
    }
  }
}

.examples{
  padding: 3em 0 2.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 1.5em;
  }
  &__view{
    display: none;
    &._active{
      display: block;
    }
  }
}

.example-card{
  text-align: center;
  &:hover{
    .example-card__img img{
      transform: scale(1.05);
    }
  }
  &__img{
    display: block;
    @include imgCover(71%);
    img{
      border-radius: .5em;
      transition: all .3s;
    }
  }
  &__content{
    margin-top: 1.125em;
  }
}

.case-item{
  padding-bottom: 1.875em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em  ;
  }
}

.info-card{
  padding: 1em 1.5em;
  border-radius: .5em;
  background-color: $lightGrayBg;
  display: flex;
  @media screen and (min-width: 993px){
    &._big{
      grid-column: span 2;
    }
  }
  &__icon{
    flex-shrink: 0;
    width: 4.625em;
    height: 4.625em;
    margin-right: .875em;
    svg{
      width: 100%;
      height: 100%;
      fill: $primary;
    }
  }
  &__text{
    p{
      line-height: inherit;
    }
  }
}

.client-need{
  padding: 2em 0;
  background-color: $lightGrayBg;
  .text{
    margin-top: .75em;
  }
  &__subtitle,
  &__desc{
    margin-top: .5em;
  }
}

.analysis{
  padding: 2em 0;
  &__grid{
    display: grid;
    grid-template-columns: 18.25em calc(100% - 38em) 18.25em;
    gap: 1.5em;
    position: relative;
  }
  &__rects{
    position: absolute;
    left: -4.625em;
    bottom: 4.625em;
    z-index: -1;
    width: 6.1875em;
  }
}

.analysis-card{
  padding: 7.9375em 0 1.875em;
  border-radius: .5em;
  background-color: $textBlack;
  color: #fff;
  text-align: center;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    left: 100%;
    top: 50%;
    width: 1.5em;
    height: 1.5em;
    transform: translate(-50%,-50%) rotate(-45deg);
    background-color: inherit;
    border-radius: .125em;
  }
  &._type2{
    background-color: $primary;
    &:before{
      left: unset;
      right: 100%;
      transform: translate(50%,-50%) rotate(-45deg);
    }
    .analysis-card__bottom{
      border-color: #fa8560;
    }
  }
  &__icon{
    position: absolute;
    top: 1.875em;
    left: 50%;
    transform: translateX(-50%);
    height: 5.3125em;
    width: 5.5em;
    svg{
      height: 100%;
      width: 100%;
      fill: #fff;
    }
  }
  &__bottom{
    margin-top: 1.25em;
    padding: 1.75em 1.5em 0;
    border-top: 1px solid #6c6c6c;
  }
  &__content{
    @include grid100gap(.625em);
  }
}

.analysis-info{
  padding: 1.5em 1.75em;
  border-radius: .5em;
  background-color: $lightGrayBg;
  text-align: center;
  .text16{
    margin-top: .5em;
  }
}

.case-group{
  &__top{
    padding: 2.25em 0 2.5em;
    background-color: $lightGrayBg;
  }
  &__info,
  &__tabs{
    position: relative;
    z-index: 2;
  }
  &__info{
    @include grid100gap(.75em);
  }
  &__tabs{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 8.25em / 4));
    gap: 2.75em;
  }
  @media screen and (min-width: 993px){
    .sector-card{
      height: 6.75em;
    }
  }
  &__bottom{
    padding: 2.25em 0;
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: .5em;
  }
  &__inner{
    position: relative;
  }
  &__rects,
  &__rects2{
    position: absolute;
    z-index: -1;
    width: 7.625em;
  }
  &__rects{
    bottom: 7.5em;
    left: -4.4375em;
  }
  &__rects2{
    bottom: 8.75em;
    right: -4.4375em;
  }
  &__rects3,
  &__rects4{
    position: absolute;
    width: 15.25em;
  }
  &__rects3{
    top: 1em;
    right: 2em;
  }
  &__rects4{
    top: 2em;
    right: 18.375em;
  }
}

.edu-settings{
  padding: 2.125em 0 2.8125em;
  background-color: $textBlack;
  color: #fff;
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(7,calc(100% / 7 - 7.5em / 7));
    gap: 1.25em;
  }
  &__item{
    padding: .5em 1.25em;
    display: flex;
    align-items: center;
    min-height: 6.75em;
    border-radius: .5em;
    background-color: $primary;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 100%;
      top: 50%;
      width: 1.125em;
      height: 1.125em;
      transform: translate(-50%,-50%) rotate(-45deg);
      background-color: inherit;
      border-radius: .125em;
    }
    &:nth-child(odd){
      color: $textBlack;
      background-color: #fff;
    }
  }
  &__title{
    line-height: 131%;
  }
}

.work-result{
  padding: 2.125em 0;
  &__info{
    @include grid100gap(.75em);
  }
  &__bottom{
    margin-top: 1.75em;
    @include grid100gap(1.25em);
  }
  .sector-card{
    background-color: $lightGrayBg;
    min-height: 7em;
    &:hover{
      cursor: default;
      svg{
        fill: #fff;
      }
    }
    &__icon{
      svg{
        fill: $primary;
      }
    }
    .text18{
      font-size: .9375em;
      line-height: 160%;
    }
  }
}

.working-now{
  padding: 1.125em 0;
  color: #fff;
  background-color: $primary;
  text-align: center;
  .text27{
    letter-spacing: 0.025em;
  }
  strong{
    display: block;
  }
  @media screen and (min-width: 993px){
    br{
      display: none;
    }
  }
}

.working-principe{
  padding: 2.25em 0;
  &__inner{
    color: #fff;
    background-image: linear-gradient(90.0deg, #ea5626 16%, #fc793f 83%);
    background-color: #f1c707;
    border-radius: .625em;
    padding: 2.625em 7em 2em;
    @include grid100gap(1em);
    text-align: center;
    position: relative;
    z-index: 2;
  }
  &__desc{
    letter-spacing: 0.025em;
    line-height: 167%;
  }
  .mission__by{
    &:before,
    &:after{
      background-color: #fff;
    }
  }
  &__quote{
    position: absolute;
    width: 5.4375em;
  }
  &__quote1{
    left: -.25em;
    top: .875em;
  }
  &__quote2{
    right: -.8125em;
    bottom: .5em;
  }
  &__rects,
  &__rects2,
  &__rects3{
    z-index: -1;
    position: absolute;
  }
  &__rects{
    left: -8.625em;
    top: -2.25em;
    width: 3.125em;
  }
  &__rects2{
    left: -3.0625em;
    top: 5.5625em;
    width: 6.125em;
  }
  &__rects3{
    top: .6875em;
    right: -6.125em;
    width: 6.3125em;
  }
}

.props{
  @include grid100gap(.375em);
  &__nav{
    display: none;
  }
}

.prop-item{
  display: flex;
  justify-content: space-between;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    border-bottom: 1px dotted $textBlack;
    width: 100%;
    height: 1px;
    bottom: .375em;
    left: 0;
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 150%;
    background-color: $lightGrayBg;
    position: relative;
    z-index: 2;
  }
  &__title{
    padding-right: .25em;
  }
  &__desc{
    padding-left: .25em;
  }
}



@media screen and (max-width: 992px){
  .cases,
  .catalog-item2{
      .tabs{
        &__current{
          margin: 0 0 1.25em;
          background-color: #ffffff;
        }
        &__dropdown{
          background-color: #ffffff;
        }
      }
    }

  .cases{
    padding: 1.25em 0;
    background-color: $lightGrayBg;

    &__content{
      padding: 0;
      margin-top: 1.25em;
    }
    &__grid{
      grid-template-columns: repeat(4,calc(25% - .875em * 3 / 4));
      gap: .875em;
    }
     &__rects,
     &__rects2{
       display: none;
     }
  }

  .tabs {
    &._type2 {
      .tabs__current{
        display: flex;
        align-items: center;
        svg{
          display: none;
        }
      }
      .tab,
      .tabs__current{
        height: 3.3125em;
        padding-left: 1.625em;
      }
      .text16{
        font-size: .875em;
      }
      .tab{
        padding-right: 1.625em;
        border-radius: 0;
        &._active{
          background-color: $primary;
          color: #fff;
        }
        svg{
          display: none;
        }
        br{
          display: none;
        }
      }
    }
  }

  .examples{
    padding: 1.25em 0 2.25em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
  }

  .case-item{
    padding-bottom: 1.25em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .info-card{
    .text16{
      font-size: .9375em;
    }
  }

  .client-need{
    padding-top: 1.5em;
  }

  .analysis{
    padding: 1.375em 0 1.75em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__rects{
      display: none;
    }
  }

  .analysis-card{
    text-align: left;
    padding: .75em 0 1.25em;
    &:before{
      display: none;
    }
    &__top{
      text-align: center;
    }
    &__bottom{
      display: flex;
      align-items: center;
      padding: 1em 1.625em 0;
    }
    &__icon{
      flex-shrink: 0;
      position: static;
      transform: unset;
      margin-right: 1em;
    }
    .text24{
      font-size: 1.375em;
    }
    &._type2{
      .analysis-card__icon{
        margin-right: .25em;
      }
    }
  }

  .analysis-info{
    padding: 1.5em;
    .text27{
      font-size: 1.375em;
    }
    .text16{
      font-size: .9375em;
    }
  }

  .we-can,
  .case-group,
  .work-result{
    .sector-card{
      flex-direction: row;
      text-align: left;
      &__icon{
        width: 4.375em;
        margin: 0 .625em 0 0;
      }
      .text18{
        line-height: 150%;
        hyphens: unset;
      }
      br{
        display: none;
      }
    }
  }

  .case-group{
    &__tabs{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }

    &__bottom{
      padding: 1.625em 0 1.75em;
    }
    &__rects,
    &__rects2,
    &__rects3,
    &__rects4{
      display: none;
    }
  }

  .edu-settings{
    padding: 1.625em 0 2.25em;
    text-align: center;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.125em;
    }
    &__item{
      min-height: 3.75em;
      justify-content: center;
      &:before{
        top: 100%;
        left: 50%;
        width: 1em;
        height: 1em;
      }
    }
  }

  .work-result{
    padding: 1.5em 0 1.75em;
    &__info{
      gap: .5em;
    }
    &__bottom{
      margin-top: 1.25em;
      gap: 1em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
  }

  .working-now{
    .text27{
      font-size: 1.5em;
    }
  }

  .working-principe{
    padding: 1.5em 0;
    &__inner{
      padding: 5em 1em;
    }
    &__quote1{
      left: .8125em;
      top: -.75em;
    }
    &__quote2{
      right: 1.4375em;
      bottom: -1em;
    }
    &__rects,
    &__rects2,
    &__rects3{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .cases{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - .875em * 2 / 3));
    }
  }

  .case-card{
    padding-top: .875em;
    padding-bottom: .75em;
    &__icon{
      height: 3.25em;
      width: 3.25em;
    }
    &__title{
      font-size: .8125em;
      line-height: 130%;
    }
  }

  .case-group{
    padding: 1.625em 0;
    &__info{
      gap: .625em;
    }
    &__tabs{
      grid-template-columns: 100%;
    }
  }

  .work-result{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
