.news-page {
  padding: 0 0 2.25em;
  &__inner {
    display: flex;
    justify-content: space-between;
    position: relative;
  }
  &__bg {
    position: absolute;
    z-index: -1;
    top: -2.9375em;
    right: -3.1875em;
    width: 15.25em;
  }
  &__left {
    width: 18.3125em;
    margin-right: 2.25em;
  }
  &__right {
    width: calc(100% - 20.5625em);
  }
  &__subtitle {
    font-size: 1.6875em;
    line-height: 140%;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #434242;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.6875em;
    margin-top: 1.4375em;
  }
  &__cards2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.0625em;
  }
  &__btn {
    height: 3.75em;
    padding: 0 4.3125em;
    margin: 1.375em auto 0;
  }
  &__nav{
    margin-bottom: 1.25em;
  }
  &__title{
    margin-bottom: .625em;
  }
}

.news-card3 {
  background-color: #f0f0f0;
  border-radius: 0.625em 0.625em 0 0;
  &__top {
    position: relative;
    padding-bottom: 61%;
  }
  &__img {
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__info {
    position: absolute;
    left: 0;
    bottom: 0.75em;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.9375em;
  }
  &__date {
    font-size: 0.75em;
    line-height: 140%;
    color: #ffffff;
  }
  &__author {
    display: flex;
    align-items: center;
    i {
      width: 0.9375em;
      img {
        display: block;
        width: 100%;
      }
    }
    span {
      font-size: 0.75em;
      line-height: 140%;
      color: #ffffff;
      margin-left: 0.3125em;
    }
  }
  &__desc {
    text-align: center;
    padding: 0.9375em 0.9375em 1.5625em 0.9375em;
  }
  &__title {
    font-size: 1.125em;
    line-height: 130%;
    font-weight: 700;
    color: #434242;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:4;
    -webkit-box-orient: vertical;
  }
}

.news-card4 {
  display: flex;
  align-items: center;
  background-color: #f5f5f5;
  border-radius: 0.625em;
  padding: 0.9375em;
  &__img {
    flex-shrink: 0;
    width: 12.875em;
    img {
      width: 100%;
    }
  }
  &__desc {
    margin-left: 1.1875em;
  }
  &__title {
    font-size: 1.125em;
    line-height: 140%;
    font-weight: 700;
    color: #434242;
  }
  &__text {
    margin-top: 0.625em;
    p {
      font-size: 16px;
      line-height: 140%;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:4;
      -webkit-box-orient: vertical
    }
  }
  &__info {
    display: flex;
    align-items: center;
    margin-top: 2.8125em;
  }
  &__author {
    display: flex;
    align-items: center;
    i {
      width: 0.9375em;
      img {
        display: block;
        width: 100%;
      }
    }
    span {
      font-size: 0.9375em;
      line-height: 120%;
      color: #8c8c8c;
      margin-left: 0.3125em;
    }
  }
  &__date {
    display: flex;
    align-items: center;
    margin-left: 1.1875em;
    i {
      width: 0.9375em;
      img {
        display: block;
        width: 100%;
      }
    }
    span {
      font-size: 0.9375em;
      line-height: 120%;
      color: #8c8c8c;
      margin-left: 0.3125em;
    }
  }
}

@media screen and (max-width: 992px) {
  .news-page {
    &__inner {
      flex-direction: column;
    }
    &__bg {
      display: none;
    }
    &__left {
      width: 100%;
      margin: 2em 0 0 0;
    }
    &__right {
      order: -1;
      width: 100%;
    }
    &__subtitle {
      font-size: 1.5em;
    }
    &__nav,
    &__title{
      display: none;
    }
  }

  .news-card3 {
    display: flex;
    border-radius: 0.625em;
    padding: 0.9375em;
    &__top {
      padding-bottom: 0;
    }
    &__img {
      flex-shrink: 0;
      width: 12.875em;
      img {
        position: static;
        height: auto;
        border-radius: 0.625em;
      }
    }
    &__desc {
      padding: 0 0.9375em;
    }
    &__info {
      display: block;
    }
    &__author {
      margin-top: 0.3125em;
    }
  }
}

@media screen and (max-width: 576px) {
  .news-card3 {
    display: block;
    &__top {
      padding-bottom: 61%;
    }
    &__img {
      width: 100%;
      img {
        position: absolute;
      }
    }
    &__desc {
      padding: 0.9375em;
    }
  }
  .news-card4 {
    display: block;
    &__img {
      width: 100%;
      position: relative;
      padding-bottom: 100%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
      }
    }
    &__desc {
      margin: 1em 0 0 0;
    }
    &__info {
      display: block;
    }
    &__date {
      margin: 0.625em 0 0;
    }
  }
}
