.solutions{
  padding-bottom: 4.5em;
  background-size: cover;
  background-position: center;
  flex-grow: 1;
  &__top{
    color: #fff;
    max-width: 71em;
    .breadcrumbs__item{
      color: #fff;
    }
  }
  .services__grid{
    margin-top: 1.75em;
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(5,calc(20% - 1em));
      gap: 1.25em;
      .text15{
        font-size: 1.125em;
      }
    }
  }
  .service-card{
    &:not(:hover){
      svg{
        fill: $black;
      }
      background-color: #fff;
    }
    &__icon{
      height: 3.75em;
    }

  }
}

.solution-item{
  padding-bottom: 1.875em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 32.25em) 30.5em;
    gap: 1.75em;
    position: relative;
  }
  &__banner{
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    display: flex;
  }
  &__img{
    width: 10.375em;
    height: 10.375em;
    padding: .75em 1.125em;
    @include center;
    border: 1px solid #b1b1b1;
    border-radius: .3125em;
    flex-shrink: 0;
    margin-right: 1.125em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__text{
    hyphens: auto;
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    margin-top: .75em;
  }
  &__text2{
    margin-bottom: .25em;
    margin-right: .25em;
  }
  &__nav{
    margin-top: 1em;
  }
  &__btn{
    width: 100%;
    height: 3.75em;
    border-radius: .3125em;
    img{
      width: 1.8125em;
      flex-shrink: 0;
      margin-right: .5625em;
    }
  }
  &__rects{
    position: absolute;
    z-index: -1;
    left: 49.75em;
    bottom: 2.5em;
    width: 6.125em;
  }
  &__fit{
    margin-top: 1.875em;
    position: relative;
  }
  &__rects2{
    position: absolute;
    z-index: -1;
    top: .25em;
    right: 4.25em;
    width: 13.4375em;
  }
  .target__items{
    margin-top: .75em;
  }
  @media screen and (min-width: 993px){
    .target-card{
      padding: 1em .75em;
      flex-direction: row;
      align-items: flex-start;
      text-align: left;
      &__icon{
        flex-shrink: 0;
        margin: 0 .5em 0 0;
      }
    }
  }
}

.breadcrumbs--mb0{
  margin-bottom: 0;
}

.functional{
  padding: 1.875em 0 2.625em;
  background-color: $lightGrayBg;
  &__inner{
    position: relative;
  }
  &__rects{
    position: absolute;
    pointer-events: none;
  }
  &__rects1{
    top: 21.125em;
    margin-left: -3.0625em;
    width: 7.625em;
  }
  &__rects2{
    top: 34.4em;
    left: 10.1875em;
    width: 12.0625em;
  }
  &__grid{
    margin-top: .875em;
    display: grid;
    grid-template-columns: calc(100% - 54.75em) 26em 26em;
    gap: 1.375em;
    position: relative;
    z-index: 2;
  }
  @media screen and (min-width: 993px){
    ._big{
      grid-row: 1/3;
      .functional-card {
        &__inner {
          align-items: center;
        }
      }
    }
    ._row{
      grid-column: 2/4;
      .functional-card{
        &__inner{
          align-items: center;
        }
        &__info{
          display: flex;
          align-items: center;
        }
      }
      .text15{
        margin: 0 0 0 1.5em;
      }
    }
  }
  .automatization-slider{
    margin-top: 1.875em;
    position: relative;
    z-index: 2;
    .swiper-slide{
      width: 100%;
    }
    &__img{
      @media screen and (min-width: 993px){
        padding-bottom: 45.8%;
      }
    }
  }
}

.functional-card{
  padding: 1.5em 1.375em;
  background-color: #ffffff;
  border-radius: .3125em;
  &__inner{
    display: flex;
  }
  &__icon{
    width: 4.1875em;
    height: 4.1875em;
    margin-right: 1em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: 100%;
      fill: $primary;
    }
  }
  .text20{
    line-height: 120%;
  }
  .text15{
    margin-top: .5em;
  }
  &__list{
    margin-top: 1.5em;
  }
}

.advantages-1c{
  padding: 2.25em 0;
  &__grid{
    margin-top: 2em;
  }
  &__item{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 2.125em;
      height: 2.125em;
      background-image: url(../img/checked.svg);
      @include bgImgSize(100%);
      margin-right: .875em;
    }
    .text16{
      letter-spacing: 0.025em;
    }
  }
  @media screen and (min-width: 577px){
    ._big{
      grid-column: span 2;
    }
  }
}


@media screen and (max-width: 992px){
  .solutions{
    padding-bottom: 2em;
  }

  .solution-item{
    &__inner{
      grid-template-columns: 100%;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .functional{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__rects{
      display: none;
    }
    ._big{
      grid-column: 1/-1;
    }
  }

  .functional-card{
    &__inner{
      display: block;
    }
    &__icon{
      margin: 0 0 .5em;
    }
    .text20{
      br{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .solution-item{
    &__img{
      width: 7em;
      height: 7em;
      padding: .5em;
    }
  }

  .advantages-1c,
  .functional {
    &__grid {
      grid-template-columns: 100%;
    }
  }
}
