.catalog-item{
  margin-top: -1em;
  padding-bottom: 1.25em;
  &__inner{
    display: grid;
    grid-template-columns: 36.6875em calc(100% - 37.9375em);
    gap: 1.25em;
    position: relative;
  }
  &__top,
  &__bottom{
    display: grid;
    grid-template-columns: 19em calc(100% - 20.25em);
    gap: 1.25em;
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
  &__btn{
    height: 3em;
    border-radius: .375em;
    span{
      font-size: 1em;
    }
    svg{
      width: 1.75em;
      height: 1.75em;
      margin-right: .625em;
    }
    &-wrap{
      position: relative;
      input{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        cursor: pointer;
        opacity: 0;
        &:checked{
          &+.catalog-item__btn{
            background-color: $textBlack;
            color: #fff;
          }
        }
      }
    }
    &._add-comparison,
    &._add-favourite{
      &._active{
        background-color: $primary;
        color: #fff;
        svg{
          fill: #fff;
        }
      }
    }
  }
  &__articul{
    min-height: 3em;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__rects{
    position: absolute;
    z-index: -1;
    width: 7.625em;
    top: 8.75em;
    left: -4.5em;
  }
  &__rects2{
    position: absolute;
    z-index: -1;
    width: 11.625em;
    top: 15.25em;
    right: 19.875em;
  }
  &__top2{
    display: grid;
    grid-template-columns: 9.125em auto auto;
    gap: .75em;
  }
  &__delivery{
    &-way{
      display: flex;
      position: relative;
      .catalog-item__btn{
        padding: 0 1.25em;
        min-width: 9.125em;
        margin-right: .625em;
      }
    }
    &-title{
      position: absolute;
      bottom: 100%;
      left: 0;
      margin-bottom: .25em;
    }
  }
}

.product-gallery{
  position: relative;
  min-height: 28.5em;
}

.gallery-item{
  background-color: #fff;
  border-radius: .375em;
  border: $border;
  @media screen and (min-width: 993px){
    &:not(._big){
      @include lastM(0 0 1em);
      width: 6.375em;
    }
    &._big{
      position: absolute;
      right: 0;
      top: 0;
      width: calc(100% - 8em);
    }
  }
  &._big{
    img{
      max-width: 70%;
      max-height: 70%;
    }
  }
  &__img{
    padding-bottom: 100%;
    position: relative;
    display: block;
    img,svg{
      @include centerAbsolute;
    }
    img{
      max-width: 91%;
      max-height: 91%;
      display: block;
    }
    svg{
      width: 2em;
      height: auto;
      fill: #fff;
      box-shadow: .0625em .125em 2.1875em 0 rgba(0,0,0,0.56);
      border-radius: 50%;
    }
  }
}

.product-props,
.product-info{
  margin-top: 1.25em;
  padding: 1.25em 1.875em 1.5em;
  background-color: $lightGrayBg;
  border-radius: .375em;
}

.product-props{
  .text{
    ul{
      @include grid100gap(.375em);
    }
  }
  &__nav{
    margin-top: .5em;
    display: flex;
  }
}

.product-info{
  padding-bottom: 1em;
  padding-top: 1.125em;
  &__top,
  &__num{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__order{
    margin-left: .5em;
  }
  &__has{
    display: flex;
    align-items: center;
    svg{
      margin-right: .4375em;
      width: 1.0625em;
      height: 1.0625em;
      fill: $green;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__nav{
    display: grid;
    grid-template-columns: 7.1875em calc(100% - 7.1875em - .625em);
    gap: .625em;
    margin-top: .75em;
    .cartcalc{
      background-color: #fff;
      height: 3em;
      width: 100%;
      grid-template-columns: 2.125em calc(100% - 4.25em) 2.125em;
      button{
        background-color: #fff;
      }
      .ccalc-minus{
        border-right: 1px solid #e4e4e4;
      }
      .ccalc-plus{
        border-left: 1px solid #e4e4e4;
      }
    }
  }
  &__btn{
    height: 3em;
    width: 100%;
    border-radius: .5em;
    span{
      font-size: .9375em;
      line-height: 130%;
    }
  }
  &__nav2{
    margin-top: .625em;
    .product-info__btn{
      height: 3.125em;
    }
  }
  &__bottom{
    margin-top: .75em;
    display: flex;
    align-items: center;
  }
  &__rating{
    display: flex;
    margin-right: .75em;
  }
  .product-card__prices-items{
    display: flex;
    align-items: flex-end;
  }
  .product-card__price2{
    margin: 0 0 -.125em .5em;
    color: $textBlack;
  }
}

.catalog-item2{
  padding: 1.25em 0;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 17.5em) 15.625em;
    gap: 1.875em;
    position: relative;
  }
  &__preview{
    &-title{
      text-align: center;
    }
  }
  .product-slider{
    margin-top: 1.25em;
    @media screen and (min-width: 993px){
      .swiper{
        &-slide{
          width: 100%;
          &-active{
            opacity: 1;
            &~.swiper-slide{
              opacity: 0;
            }
          }
        }
        &-nav{
          display: flex;
          justify-content: center;
          margin-top: .875em;
          .swiper-button{
            transform: unset;
            position: static;
            margin: 0;
            width: 2.75em;
            height: 2.75em;
            svg{
              max-width: 1.5em;
              max-height: 1.5em;
            }
          }
        }
      }
    }
  }
  &__rects{
    position: absolute;
    z-index: -1;
    width: 7.625em;
    top: 13.5625em;
    left: -4.4375em;
  }
}

.catalog-desc{
  @include grid100gap(1em);
  p{
    line-height: inherit;
  }
}

.review{
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 19.5em) 18.25em;
    gap: 1em 1.25em;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
  }
  &__btn{
    height: 3.75em;
    width: 14.375em;
    svg{
      width: 1.5em;
      height: 1.5em;
    }
  }
}

.review-form{
  padding: 1.25em 1.75em;
  background-color: #ffffff;
  border-radius: .5em;
  margin-top: 1.25em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
    margin-top: 1.25em;
  }
  ._full{
    grid-column: 1/-1;
  }
  &__rating{
    display: flex;
    align-items: center;
    .rating-input{
      margin-right: .625em;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: .875em;
  }
  &__btn{
    width: 14.375em;
    height: 3.3125em;
    margin-right: 1em;
  }
}

.review-result{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1.5em;
  .rating-input{
    margin-top: .5em;
    &__item{
      width: 1.6875em;
      height: 1.6875em;
      margin-right: .4375em;
    }
  }
}

.feedback{
  margin-top: .75em;
  &__items{
    margin-top: 1em;
    @include grid100gap(1em);
  }
  &__nav{
    margin-top: 1em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    width: 14.375em;
    height: 3.3125em;
  }
}

.feedback-card{
  padding: 1.25em;
  background-color: #fff;
  border-radius: .3125em;
  &._new{
    display: none;
  }
  &__inner{
    display: flex;
  }
  &__img{
    flex-shrink: 0;
    width: 6.875em;
    height: 6.875em;
    border-radius: 50%;
    margin-right: 1.5em;
    background-color: $grayBg;
    background-image: url(../img/man.svg);
    @include bgImgSize(49%);
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
      display: block;
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__date{
    margin: 0 1.25em;
    flex-shrink: 0;
    color: #8a8a8a;
  }
  &__text{
    margin-top: .5em;
  }
  &__gallery{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .875em);
    &-item{
      margin: .875em .875em 0 0;
      width: 5.125em;
    }
    &-img{
      display: block;
      @include imgCover(100%);
      img{
        border-radius: .5em;
      }
    }
  }
}

.product-payment{
  .payment-card{
    &._gray{
      background-color: #fff;
    }
  }
  @media screen and (min-width: 993px){
    .payment__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 1.5em;
    }
    .payment-card{
      &__top{
        height: unset;
      }
      &__icon{
        img {
          width: 3.0625em;
        }
      }
      &__title{
        span{
          font-size: 1.125em;
        }
      }
      &__item{
        flex-direction: column;
        align-items: flex-start;
        padding: .875em 1.5em;
      }
      &__subtitle{
        margin: .625em 0 0;
      }
      &__bottom{
        height: unset;
      }
      &._red{
        padding: 1.5em;
      }
    }
    .payment-card__bottom--2{
      flex-grow: 1;
      display: grid;
      grid-template-columns: repeat(2,50%);
      grid-template-rows: repeat(2,50%);
      .payment-card__item{
        width: 100%;
        align-items: center;
        flex-direction: row;
        padding: 1em;
        &:last-child{
          grid-column: 1/-1;
          border-top: 1px solid #d3d3d3;
        }
        .payment-card__icon{
          img{
            width: 1.4375em;
          }
        }
        .payment-card__subtitle{
          margin: 0 0 0 .5em;
        }
      }
    }
  }

}

.product-delivery{
  display: grid;
  grid-template-columns: calc(100% - 29.25em) 27.5em;
  gap: 0 1.75em;
  .free-delivery{
    background-color: #fff;
  }
  .pay-delivery{
    background-color: #fff;
    tr:nth-child(odd) td{
      background-color: $lightGrayBg;
    }
  }
  &__text{
    margin-top: 1.5em;
  }
}

.delivery-duration{
  background-color: #fff;
  padding: 1.25em;
  border-radius: .5em;
  margin-top: 1em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 2.0625em;
    height: 2.0625em;
    flex-shrink: 0;
    margin-right: .375em;
    svg{
      width: 100%;
      height: 100%;
      fill: $primary;
    }
  }
  &__title{
    display: flex;
    align-items: center;
    padding: 0 .6875em;
    border: 1px solid #e5e5e5;
    border-radius: .5em;
    min-height: 1.8125em;
    color: #878787;
    .text16{
      line-height: 120%;
    }
  }
  &__text{
    margin-top: 1em;
  }
}

.collection{
  padding: 1.25em 0 2em;
  overflow: hidden;
  .product-slider{
    margin-top: 1em;
  }
}

.sale-badge{
  position: absolute;
  bottom: 1.375em;
  right: 0;
  width: calc(100% - 8em);
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 1.625em;
  z-index: 2;
  &__wrap{
    color: #ffffff;
    white-space: nowrap;
    min-width: 11.75em;
    background-color: $primary;
    padding: .25em 1.125em .25em 1.5em;
    border-radius: 1.8125em 0 0 1.8125em;
  }
  &__prices{
    display: flex;
    align-items: center;
  }
  &__price2{
    margin-left: .625em;
    text-decoration: line-through;
    color: #fdbca8;
  }
}



@media screen and (max-width: 992px){
  .catalog-item{
    &__inner{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__col{
      &._full{
        grid-column: 1/-1;
      }
    }
    &__rects,
    &__rects2{
      display: none;
    }
    &__top2{
      margin-top: 1.25em;
    }
  }

  .product-gallery{
    min-height: unset;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
    gap: .75em .625em;
    max-width: 30em;
    margin: 0 auto;
  }

  .gallery-item{
    &._big{
      grid-column: 1/-1;
    }
  }

  .catalog-item2 {
    padding: 0;
    &__inner {
      grid-template-columns: 100%;
    }
    &__preview{
      padding: 1.25em 0;
    }
    &__content{
      width: calc(100% + 3.75em);
      padding-top: 1.25em;
      padding-bottom: 1.25em;
      background-color: $lightGrayBg;
      @include fullScreen;
    }
    &__rects{
      display: none;
    }
  }

  .props{
    &__nav{
      display: flex;
      margin-top: 1em;
    }
    &:not(.show-all){
      .prop-item:nth-child(6)~.prop-item{
        display: none;
      }
    }
  }

  .prop-item{
    display: block;
    &:before{
      display: none;
    }
    &__title{
      padding: 0 0 .25em;
      font-size: .9375em;
      color: #6d6d6d;
      &:after{
        content: ':';
      }
    }
    &__desc{
      padding: 0;
    }
  }

  .review{
    &__top{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }

  .review-form{
    &__grid{
      grid-template-columns: 100%;
    }
    &__nav{
      @include grid100gap(1em);
      .file-input{
        order: -1;
      }
    }
    &__btn{
      margin: 0;
      width: 100%;
    }
  }

  .review-result{
    align-items: flex-start;
    .text14{
      font-size: 1em;
    }
    .rating-input{
      &__item{
        width: 1.4375em;
        height: 1.4375em;
        margin-right: .3125em;
      }
    }
  }

  .feedback{
    &__items{
      margin-top: .875em;
      gap: .875em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .product-delivery{
    grid-template-columns: 100%;
    ._mob-hide{
      display: none;
    }
    &__text{
      margin-top: 1em;
    }
  }

  .delivery-duration{
    padding: 1.25em 1.25em;
    &__top{
      align-items: unset;
    }
    &__title{
      padding-top: .375em;
      padding-bottom: .375em;
    }
    &__text{
      margin-top: .875em;
      .text16{
        line-height: 120%;
      }
    }
  }

  .sale-badge{
    padding-left: 1.25em;
    padding-bottom: 1em;
    width: 100%;
    top: 30em;
    bottom: unset;
    transform: translateY(-100%);
  }
}


@media screen and (max-width: 576px){
  .catalog-item {
    &__top,
    &__bottom{
      grid-template-columns: 100%;
      gap: .75em;
    }
    &__articul{
      min-height: unset;
      justify-content: unset;
    }
    &__top2{
      grid-template-columns: 100%;
      margin: 0;
    }
    &__delivery{
      &-way{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .375em));
        gap: .25em .75em;
        .catalog-item__btn{
          padding: 0;
          margin: 0;
          min-width: unset;
          width: 100%;
        }
      }
      &-title{
        position: static;
        grid-column: 1/-1;
      }
    }
  }

  .product-props,
  .product-info{
    padding-left: 1.25em;
    padding-right: 1.25em;
    margin-top: .875em;
  }

  .product-info{
    margin-top: .3125em;
  }

  .feedback{
    &__btn{
      width: 100%;
      height: 4em;
    }
  }

  .feedback-card{
    position: relative;
    &__inner{
      display: block;
    }
    &__img{
      position: absolute;
      top: 1.25em;
      left: 1.25em;
    }
    &__top{
      min-height: 6.875em;
      padding-left: 7.875em;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }
    &_date{
      margin: .75em 0;
    }
    &__rating{
      display: flex;
    }
  }

  .sale-badge{
    top: 22.375em;
    &__text{
      max-width: 7.4em;
    }
  }

  .collection{
    .text27{
      font-size: 1.25em;
      text-align: center;
    }
  }
}
