.news-item {
  padding-bottom: 2.75em;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 18.3125em;
    margin-right: 2.25em;
  }
  &__right {
    width: calc(100% - 20.5625em);
  }
  &__content{
    background-color: #f5f5f5;
    border-radius: 0.625em;
    padding: 1.5625em;
  }
  &__group{
    @include lastM(0 0 1.625em);
  }
  &__title{
    margin-bottom: .5em;
    max-width: 26.8125em;
  }
  .automatization-slider{
    margin-top: 1.875em;
    position: relative;
    z-index: 2;
    .swiper-slide{
      width: 100%;
    }
    &__img{
      @media screen and (min-width: 993px){
        padding-bottom: 46.9%;
      }
    }
    @media screen and (min-width: 993px){
      width: calc(100% - 5em);
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.news-text {
  &__inner {
    position: relative;
  }
  &__bg {
    position: absolute;
    z-index: -1;
    top: 8em;
    right: -8.125em;
    width: 15.25em;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__img {
    width: 20.875em;
    margin-right: 1.25em;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 22.125em);
  }
  &__info {
    display: flex;
    align-items: center;
    &-img {
      flex-shrink: 0;
      width: 5.875em;
      height: 5.875em;
      background-color: #ffffff;
      border-radius: 0.625em;
      padding: 0.625em 1.25em;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 0.875em;
    }
  }
  &__author {
    display: flex;
    align-items: center;
    i {
      width: 0.9375em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.125em;
      line-height: 120%;
      color: #434242;
      margin-left: 0.4375em;
    }
  }
  &__date {
    display: flex;
    align-items: center;
    margin-top: 0.625em;
    i {
      width: 1em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 0.9375em;
      line-height: 120%;
      color: #8c8c8c;
      margin-left: 0.4375em;
    }
  }
  &__nav {
    display: flex;
    align-items: center;
    margin-top: 0.625em;
  }
  &__favorites {
    display: flex;
    align-items: center;
    &-btn {
      width: 1.125em;
      padding: 0;
      background: transparent;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.125em;
      line-height: 120%;
      color: #434242;
      margin-left: 0.3125em;
    }
  }
  &__comment {
    display: flex;
    align-items: center;
    margin-left: 0.9375em;
    &-btn {
      width: 1em;
      padding: 0;
      background: transparent;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.125em;
      line-height: 120%;
      color: #434242;
      margin-left: 0.3125em;
    }
  }
  &__text {
    margin-top: 2em;
    p {
      font-size: 1em;
      line-height: 140%;
      color: #434242;
      &:not(:first-child) {
        margin-top: 1.25em;
      }
    }
  }
  &__list {
    margin-top: 1.5em;
    li {
      font-size: 1em;
      line-height: 140%;
      color: #434242;
      position: relative;
      padding-left: 0.625em;
      &:after {
        content: '';
        position: absolute;
        top: 0.4375em;
        left: 0;
        width: 0.3125em;
        height: 0.3125em;
        background: #e55021;
        border-radius: 50%;
      }
    }
  }
  &__quote {
    background-color: #ffffff;
    border-radius: 0.3125em;
    padding: 2.5em;
    position: relative;
    margin-top: 1.625em;
    p {
      font-size: 1em;
      line-height: 140%;
      font-style: italic;
      color: #434242;
      position: relative;
      z-index: 1;
    }
    &-img {
      position: absolute;
      top: -0.625em;
      left: 0.875em;
      width: 5.1875em;
    }
    &-img2 {
      position: absolute;
      right: 0.875em;
      bottom: -0.625em;
      width: 5.1875em;
    }
  }
}

.news-statistics {
  margin-top: 1.5625em;
  &__top {
    padding: 1.875em 2.5em;
    position: relative;
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__title {
    color: #ffffff;
    position: relative;
    z-index: 1;
    span {
      display: block;
      font-weight: 400;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2.5em / 2));
    gap: 2.5em;
    margin-top: 1.5em;
  }
  &__img {
    img {
      width: 100%;
    }
    span {
      display: block;
      font-size: 0.875em;
      line-height: 1.125em;
      color: #434242;
      text-align: center;
      margin-top: 0.875em;
    }
  }
}

.news-review {
  margin-top: 1.5625em;
  &__top {
    display: flex;
    justify-content: space-between;
    background-color: #ea5626;
    border-radius: 0.625em;
    padding: 1.75em;
    margin-bottom: 2.125em;
  }
  &__info {
    p {
      font-size: 1em;
      line-height: 140%;
      color: #ffffff;
      span {
        font-weight: 700;
      }
    }
  }
  &__btn {
    flex-shrink: 0;
    height: 3.75em;
    background-color: #ffffff;
    border-radius: 0.3125em;
    padding: 0 1.5625em;
    margin-left: 2em;
    span {
      font-size: 1em;
      line-height: 120%;
      color: #434242;
    }
  }
}

.review-form__estimation {
  padding-left: 2em;
  &-title {
    font-size: 0.875em;
    line-height: 120%;
    color: #434242;
  }
  &-text {
    p {
      font-size: 0.75em;
      line-height: 120%;
      color: #7b7b7b;
    }
  }
}

.news-feedback {
  margin-top: 1.625em;
  &__title {
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 700;
    color: #434242;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.9375em;
    margin-top: 1.25em;
  }
  .feedback-card__text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
  .feedback-card__nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 0.3125em;
  }
  .feedback-card__answer-btn {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 1.5em;
      img {
        display: block;
        width: 100%;
      }
    }
    span {
      margin-left: 0.4375em;
    }
  }
  .feedback-card__favorite {
    width: 1.5em;
    margin-left: 1.25em;
    img {
      display: block;
      width: 100%;
    }
  }
  .feedback-card__answer {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.9375em;
    margin-top: 0.9375em;
    padding-left: 5.625em;
  }
  .feedback-card__answer-user {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5em;
  }
  .feedback-card__user {
    display: flex;
    align-items: center;
    span {
      font-size: 0.875em;
      line-height: 140%;
      color: #434242;
    }
    i {
      width: 1.0625em;
      margin-left: 0.3125em;
      img {
        display: block;
        width: 100%;
      }
    }
  }
  .feedback-card__user2 {
    margin-left: 1.25em;
    span {
      font-size: 0.875em;
      line-height: 140%;
      color: #ea5626;
    }
  }
  &__btn {
    height: 3.3125em;
    padding: 0 3.75em;
    margin: 1.25em auto 0;
    span {

    }
  }
}


@media screen and (max-width: 992px) {
  .news-item {
    padding-bottom: 1.5em;
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      margin: 2em 0 0 0;
    }
    &__right {
      width: 100%;
      order: -1;
    }
    &__title{
      font-size: 1.5em;
    }
  }

  .news-text {
    &__bg {
      display: none;
    }
    &__img {
      width: 12.5em;
      margin-right: 1.25em;
    }
    &__desc {
      width: calc(100% - 13.75em);
    }
  }

  .news-statistics {
    &__top {
      background: #f46435;
    }
    &__title {
      font-size: 1.5em;
      span {
        display: inline;
      }
    }
    &__bg {
      display: none;
    }
    &__images {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
  }

  .news-review {
    &__top {
      display: block;
    }
    &__btn {
      margin: 1em 0 0 0;
    }
  }

  .review-form__estimation {
    padding-left: 0;
  }

  .news-feedback {
    .feedback-card__answer {
      padding-left: 3.125em;
    }
    .feedback-card__answer-user {
      display: block;
    }
    .feedback-card__user2 {
      margin: 0.1875em 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .news-text {
    &__flex {
      display: block;
    }
    &__img {
      width: 100%;
      margin: 0 0 1em 0;
    }
    &__desc {
      width: 100%;
    }
    &__info {
      display: block;
      &-desc {
        margin: 1em 0 0 0;
      }
    }
    &__quote {
      padding: 2em;
    }
  }

  .news-statistics {
    &__top {
      padding: 1.25em;
    }
    &__images {
      grid-template-columns: 100%;
    }
  }

  .news-review {
    &__top {
      padding: 1.25em;
    }
    &__btn {
      padding: 0 1em;
    }
  }

  .news-feedback {
    .feedback-card__answer {
      padding-left: 0;
      .feedback-card {
        padding: 6em 1.25em 1.25em 1.25em;
        &__img {
          top: 6em;
        }
      }
    }
    .feedback-card__answer-user {
      position: absolute;
      top: 1.25em;
      left: 1.25em;
      padding-right: 1.25em;
    }
  }
}
