@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    border-radius: .5em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    svg{
        fill: #fff;
        width: 1.375em;
        max-height: 1.375em;
        margin-right: .6875em;
    }
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-black{
    background-color: $textBlack;
    color: #ffffff;
    &:hover{
        background-color: darken($textBlack,10%);
        color: #ffffff;
    }
}

.m-btn-gray{
    background-color: $lightGrayBg;
    color: $textBlack;
    &:hover{
        background-color: darken($lightGrayBg,10%);
        color: $textBlack;
    }
    svg{
        fill: $primary;
    }
}

.favourite-btn{
    cursor: pointer;
    transition: all .3s;
    position: relative;
    &__icon{
        transition: all .3s;
        display: block;
        width: 1.4375em;
        height: 1.25em;
        position: relative;
        &:hover{
            &:before{
                background-image: url(../img/favourite2.png);
            }
        }
        &:after,
        &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            @include bgImgSize(100%);
            transition: all .3s;
        }
        &:after{
            opacity: 0;
            z-index: 2;
            background-image: url(../img/favourite3.png);
        }
        &:before{
            background-image: url(../img/love.svg);
        }
    }
    &._added{
        .favourite-btn__icon{
            &:after{
                opacity: 1;
            }
            &:before{
                opacity: 0;
            }
        }
        &:hover{
            .favourite-btn__tooltip{
                opacity: 1;
                pointer-events: unset;
            }
        }
    }
    &__tooltip{
        position: absolute;
        white-space: nowrap;
        bottom: 99%;
        left: 50%;
        transform: translateX(-50%);
        z-index: 2;
        padding-bottom: .75em;
        opacity: 0;
        pointer-events: none;
        transition: all .3s;
    }
    &__hint{
        letter-spacing: 0.025em;
        background-color: #ffffff;
        box-shadow: .25em .4375em 1.6875em 0 rgba(91,92,92,0.25);
        height: 2.75em;
        border-radius: .625em;
        padding: .5em 1em;
        @include center;
        position: relative;
        &:before{
            content: '';
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translate(-50%,-50%) rotate(-45deg);
            background-color: #fff;
            border-radius: .25em;
            width: 1em;
            height: 1em;
        }
    }
}

.comparison-btn{
    &._added{
        svg{
            fill: $primary;
        }
    }
}

.text-link{
    color: $primary;
    text-decoration: underline;
    span{
        font-size: 1em;
        line-height: 150%;
        letter-spacing: 0.025em;
    }
    &:hover{
        text-decoration: none;
    }
}
